import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Card from 'antd/es/card'
import pullData from '../../utils/pullData'
import Descriptions from 'antd/es/descriptions'
import Post from '../../components/Post/Post'
import UserButton from '../../components/UserButton/UserButton'
import ImageCarousel from '../../components/UI/ImageCarousel/ImageCarousel'
import Helmet from 'react-helmet'

const { Item } = Descriptions

const Perk = props => {
    const [data, setData] = useState({})
    const { account } = props
    useEffect(() => {
        const pathParts = props.history.location.pathname.split('/')
        if (pathParts.length > 3) {
            const accountId = pathParts[2]
            if (props.isAuthed && account._id === accountId) {
                const perk = account.perks.find(
                    item => item.perkId === pathParts[3]
                )
                setData({ profile: account, perk })
            } else {
                pullData(props, data, setData, { forceAuth: false }, data => {
                    const friendData = {}
                    for (let friend of data.friendData)
                        friendData[friend._id] = friend

                    props.setData({
                        post: { [data.post._id]: data.post },
                        friendData,
                    })
                })
            }
        }
    }, [props.isAuthed])
    if (!data.perk) return null
    const { perk, profile } = data
    props.setData({ currentPosts: perk.post })
    if (perk) {
        props.setPageTitle({ main: perk.name, sub: profile._id })
    }

    const getAccounts = items => {
        if (profile && props.friendData) {
            return items.map(item => <UserButton id={item} />)
        } else return items
    }

    return perk ? (
        <div className="contentDiv">
            <Helmet>
                <title>
                    {perk.name} by {profile.id}
                </title>
                <meta name="description" content={perk.description} />
            </Helmet>
            <Card
                title={
                    <span>
                        Item from <UserButton id={profile._id} /> shop
                    </span>
                }
                bordered={false}
            >
                <ImageCarousel images={perk.images} />

                <h1>{perk.name}</h1>
                <p style={{ whiteSpace: 'pre-wrap' }}>{perk.description}</p>

                <Descriptions
                    title="Goal description"
                    bordered
                    column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
                >
                    <Item label="Available for">{getAccounts(perk.users)}</Item>
                    <Item label="Price">{perk.price}</Item>
                </Descriptions>
                <Post
                    noStartMessage={true}
                    post={props.post[perk.post]}
                    account={props.account}
                    friendData={props.friendData}
                    showReplies={true}
                />
            </Card>
        </div>
    ) : null
}
const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        post: state.post,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => {
            dispatch(actions.setData(data))
        },
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Perk)
)
