import React from 'react'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import * as wsActions from '../../websocketActions'

import Tabs from 'antd/es/tabs'
import DashboardPosts from './DashboardPosts'
import DashboardEvents from './DashboardEvents'
import Badge from 'antd/es/badge'
import Helmet from 'react-helmet'

const Progress = props => {
    const { account, lastNotifications } = props

    if (!account._id) return null

    props.setPageTitle({ main: 'Dashboard', sub: '' })

    let numberOfUnseenNots = 0
    for (let not of lastNotifications) {
        if (parseInt(not.notId) > parseInt(account.lastSeenNot || 0))
            numberOfUnseenNots++
    }

    return (
        <div className="contentDiv">
            <Helmet>
                <title>Addspire Dashboard with with great posts and news</title>
                <meta
                    name="description"
                    content="Addspire is a social network where you can inspire and motivate yourself or the people you care."
                />
            </Helmet>
            <Tabs
                onChange={activeTab => {
                    if (activeTab === 'events') {
                        const last = parseInt(
                            lastNotifications.reduce((res, not) => {
                                return parseInt(not.notId) > parseInt(res)
                                    ? not.notId
                                    : res
                            }, 0)
                        )
                        if (
                            lastNotifications.length > 0 &&
                            parseInt(account.lastSeenNot) !== last
                        ) {
                            wsActions.setLastSeenNot(account._id, last)
                        }
                    }
                }}
            >
                <Tabs.TabPane tab="Posts" key="posts">
                    <DashboardPosts />
                </Tabs.TabPane>

                <Tabs.TabPane
                    tab={
                        <Badge count={numberOfUnseenNots} offset={[10, 0]}>
                            Events
                        </Badge>
                    }
                    key="events"
                >
                    <DashboardEvents />
                </Tabs.TabPane>
            </Tabs>
        </div>
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
        lastNotifications: state.lastNotifications,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Progress)
