import React, { useState, useEffect } from 'react'
import { withRouter, Link } from 'react-router-dom'

import { connect } from 'react-redux'
import Menu from 'antd/es/menu'
import Avatar from 'antd/es/avatar'
import Drawer from 'antd/es/drawer'
import Button from 'antd/es/button'
import Badge from 'antd/es/badge'
import * as classes from './Layout.module.css'
import CoffeeOutlined from '@ant-design/icons/CoffeeOutlined'
import LogoutOutlined from '@ant-design/icons/LogoutOutlined'
import UserOutlined from '@ant-design/icons/UserOutlined'
import HeartFilled from '@ant-design/icons/HeartFilled'
import RocketFilled from '@ant-design/icons/RocketFilled'
import TeamOutlined from '@ant-design/icons/TeamOutlined'
import MenuOutlined from '@ant-design/icons/MenuOutlined'
import ShopFilled from '@ant-design/icons/ShopFilled'
import HomeFilled from '@ant-design/icons/HomeFilled'
import SettingFilled from '@ant-design/icons/SettingFilled'
import getAvatar from '../../utils/getAvatar'
import LoginModal from './LoginModal'
import Spin from 'antd/es/spin'
import BackTop from 'antd/es/back-top'
import LoadingOutlined from '@ant-design/icons/LoadingOutlined'
import UpOutlined from '@ant-design/icons/UpOutlined'
import PlusOutlined from '@ant-design/icons/PlusOutlined'

import Modal from 'antd/es/modal/Modal'
import AddProgress from '../../containers/Progresses/AddProgress'

const AccountLayout = props => {
    const { account } = props
    const [menuVisible, setMenuVisible] = useState()
    const [lastScrollY, setLastScrollY] = useState(0)
    const [headerVisible, setHeaderVisible] = useState(true)
    const [loginVisible, setLoginVisible] = useState(false)
    const [startProgressVisible, setStartProgressVisible] = useState(false)
    const [pageAddress, setPageAddress] = useState(
        props.history.location.pathname
    )

    useEffect(() => {
        const handleScroll = () => {
            const currentScrollY = window.scrollY
            if (window.scrollY < 100) setHeaderVisible(true)
            else {
                if (currentScrollY > lastScrollY) {
                    setHeaderVisible(false)
                } else {
                    setHeaderVisible(true)
                }
            }
            setLastScrollY(currentScrollY)
        }
        window.removeEventListener('scroll', handleScroll)
        window.addEventListener('scroll', handleScroll)
        if (pageAddress !== props.history.location.pathname) {
            window.scrollTo({
                top: 0,
                left: 0,
                behavior: 'smooth',
            })
            setPageAddress(props.history.location.pathname)
        }
        return () => window.removeEventListener('scroll', handleScroll)
    }, [lastScrollY, props.history.location.pathname])
    const toggleMenu = () => {
        setMenuVisible(!menuVisible)
    }
    const selectedKey =
        props.history.location.pathname.split('/')[1] || undefined

    const buttonProps = {
        type: 'link',
        // ghost: true,
        size: 'large',

        className: classes.Button,
    }
    const checkHighligh = url =>
        selectedKey === url
            ? {
                  ghost: false,
                  type: 'link',
              }
            : { type: 'text' }
    const openLink = url => props.history.push('/' + url)

    const isActivating = props.history.location.pathname === '/activate-account'

    let numberOfUnseenNots = 0

    for (let not of props.lastNotifications) {
        if (parseInt(not.notId) > parseInt(account.lastSeenNot || 0))
            numberOfUnseenNots++
    }
    const logedIn = props.isAuthed && account._id
    const editGoal = (
        <Modal
            title="Add progress"
            visible={startProgressVisible}
            maskClosable
            closable
            onCancel={() => setStartProgressVisible(false)}
            width="100%"
            style={{ maxWidth: '1000px' }}
            footer={null}
            destroyOnClose={true}
        >
            <AddProgress
                setStartProgressVisible={setStartProgressVisible}
                account={account}
                friendData={props.friendData}
                cancel={() => setStartProgressVisible(false)}
            />
        </Modal>
    )

    return (
        <>
            <div className={classes.Layout}>
                <header
                    className={
                        headerVisible
                            ? classes.Header
                            : [classes.Header, classes.HideHeader].join(' ')
                    }
                >
                    <div className={classes.TopHeader}>
                        <div className={classes.MenuButtonContiner}>
                            <Link to="/">
                                <img
                                    style={{ height: '30px' }}
                                    src="/logo.jpg"
                                />
                            </Link>
                            {logedIn &&
                                (!isActivating && (
                                    <Button
                                        {...buttonProps}
                                        icon={
                                            <MenuOutlined
                                                style={{ fontSize: '20px' }}
                                            />
                                        }
                                        style={{ width: '60px' }}
                                        onClick={toggleMenu}
                                        size="large"
                                    />
                                ))}
                        </div>
                        <div className={classes.QuickButtonContiner}>
                            {(logedIn || !props.isConnectingWS) && (
                                <div className={classes.PageTitle}>
                                    {isActivating ? (
                                        'Activate your account'
                                    ) : (
                                        <>
                                            <Button
                                                type="default"
                                                icon={<PlusOutlined />}
                                                shape="round"
                                                onClick={() => {
                                                    account._id
                                                        ? setStartProgressVisible(
                                                              true
                                                          )
                                                        : setLoginVisible(true)
                                                }}
                                            >
                                                Add inspiration
                                            </Button>
                                            {editGoal}
                                        </>
                                    )}
                                </div>
                            )}
                            {logedIn ? (
                                !isActivating ? (
                                    <span
                                        onClick={() => openLink('account')}
                                        style={{
                                            margin: '0px 10px',
                                            cursor: 'pointer',
                                        }}
                                    >
                                        <Avatar
                                            style={{
                                                border: '1px solid white',
                                            }}
                                            src={getAvatar(
                                                account._id,
                                                account.image
                                            )}
                                        />
                                    </span>
                                ) : (
                                    <Button
                                        {...buttonProps}
                                        {...checkHighligh('friends')}
                                        icon={<LogoutOutlined />}
                                        onClick={() =>
                                            props.history.push('/logout')
                                        }
                                    >
                                        Logout
                                    </Button>
                                )
                            ) : props.isConnectingWS ? (
                                <>
                                    <span style={{ marginRight: '30px' }}>
                                        <Spin
                                            indicator={
                                                <LoadingOutlined
                                                    style={{
                                                        color: 'white',
                                                        marginRight: '10px',
                                                    }}
                                                />
                                            }
                                        />{' '}
                                        Trying to login
                                    </span>
                                    <Button
                                        {...buttonProps}
                                        {...checkHighligh('friends')}
                                        icon={<LogoutOutlined />}
                                        onClick={() =>
                                            props.history.push('/logout')
                                        }
                                    >
                                        Logout
                                    </Button>
                                </>
                            ) : (
                                <Button
                                    {...buttonProps}
                                    {...checkHighligh('friends')}
                                    icon={<LogoutOutlined />}
                                    onClick={() => setLoginVisible(true)}
                                >
                                    Sign in or Sign up
                                </Button>
                            )}
                        </div>
                    </div>
                </header>
                <main className={classes.Main}>{props.children}</main>
                {!isActivating && logedIn && (
                    <footer className={classes.Footer}>
                        <Button
                            {...buttonProps}
                            {...checkHighligh('dashboard')}
                            onClick={() => openLink('dashboard')}
                        >
                            <Badge count={numberOfUnseenNots}>
                                <HomeFilled />{' '}
                                <div className={classes.FooterButton}>
                                    Dashboard
                                </div>
                            </Badge>
                        </Button>

                        <Button
                            {...buttonProps}
                            {...checkHighligh('inspirations')}
                            onClick={() => openLink('inspirations')}
                        >
                            <Badge count={0}>
                                <RocketFilled />
                                <div className={classes.FooterButton}>
                                    Inspirations
                                </div>
                            </Badge>
                        </Button>

                        <Button
                            {...buttonProps}
                            {...checkHighligh('shop')}
                            onClick={() => openLink('shop')}
                        >
                            <Badge count={0}>
                                <ShopFilled />
                                <div className={classes.FooterButton}>Shop</div>
                            </Badge>
                        </Button>

                        <Button
                            {...buttonProps}
                            {...checkHighligh('friends')}
                            onClick={() => openLink('friends')}
                        >
                            <Badge
                                count={
                                    account.friends.filter(
                                        item => item.status === 'inviting'
                                    ).length
                                }
                            >
                                <TeamOutlined />
                                <div className={classes.FooterButton}>
                                    Friends
                                </div>
                            </Badge>
                        </Button>
                    </footer>
                )}
                {!isActivating && logedIn && (
                    <Drawer
                        closable={true}
                        onClose={toggleMenu}
                        visible={menuVisible}
                        placement="left"
                        title={
                            <Link to="/">
                                <img
                                    style={{ width: '77px' }}
                                    src="/addspire-logo.jpg"
                                />
                            </Link>
                        }
                    >
                        <Menu
                            theme="light"
                            mode="vertical"
                            selectedKeys={[selectedKey || 'main']}
                            onClick={({ key }) => {
                                props.history.push('/' + key)
                                toggleMenu()
                            }}
                            overflowedIndicator={<MenuOutlined />}
                        >
                            <Menu.Item key="main" icon={<CoffeeOutlined />}>
                                Explore
                            </Menu.Item>
                            <Menu.Item key="dashboard" icon={<HomeFilled />}>
                                Dashboard
                            </Menu.Item>
                            <Menu.Item
                                key="inspirations"
                                icon={<RocketFilled />}
                            >
                                Inspirations
                            </Menu.Item>
                            <Menu.Item key="shop" icon={<ShopFilled />}>
                                Shop
                            </Menu.Item>
                            <Menu.Item key="friends" icon={<TeamOutlined />}>
                                Friends
                            </Menu.Item>
                            {/* <Menu.Item key="goals" icon={<AimOutlined />}>
                                Goals
                            </Menu.Item> */}
                            <Menu.Item key="wishlist" icon={<HeartFilled />}>
                                Wishlist
                            </Menu.Item>
                            <Menu.Item key="account" icon={<UserOutlined />}>
                                Account
                            </Menu.Item>
                            <Menu.Item key="settings" icon={<SettingFilled />}>
                                Settings
                            </Menu.Item>
                            <Menu.Item key="logout" icon={<LogoutOutlined />}>
                                Logout
                            </Menu.Item>
                        </Menu>
                    </Drawer>
                )}
            </div>
            <LoginModal
                loginVisible={loginVisible}
                setLoginVisible={setLoginVisible}
            />
            <BackTop>
                <div
                    style={{
                        height: 40,
                        width: 40,
                        lineHeight: '40px',
                        borderRadius: '100%',
                        backgroundColor: '#1088e9',
                        color: '#fff',
                        textAlign: 'center',
                        fontSize: 14,
                    }}
                >
                    <UpOutlined />
                </div>
            </BackTop>
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        pageTitle: state.pageTitle,
        isAuthed: state.isAuthed,
        lastNotifications: state.lastNotifications,
        isConnectingWS: state.isConnectingWS,
        friendData: state.friendData,
    }
}

export default withRouter(connect(mapStateToProps)(AccountLayout))
