import React from 'react'
import Descriptions from 'antd/es/descriptions'

import * as wsActions from '../../websocketActions'
import Rewards from '../../components/Rewards/Rewards'
import UserButton from '../../components/UserButton/UserButton'
import Divider from 'antd/es/divider'
import Tabs from 'antd/es/tabs'
import Tag from 'antd/es/tag'
import Radio from 'antd/es/radio'
import Badge from 'antd/es/badge'
import moment from 'moment'

moment().format()

const ActivityTable = props => {
    const { progress, account } = props
    const { goal } = progress

    const isOwner = account && account._id === progress.owner

    let lastStages = progress.stages
        .slice(Math.max(progress.stages.length - 7, 0))
        .sort((a, b) => {
            let aS, aF, bS, bF
            if (a.old && !b.old) return 1
            else if (b.old && !a.old) return -1
            else {
                if (a.day) {
                    aS = moment()
                        .year(a.year)
                        .dayOfYear(a.day)
                        .startOf('day')
                    aF = moment()
                        .year(a.year)
                        .dayOfYear(a.day)
                        .endOf('day')
                    bS = moment()
                        .year(b.year)
                        .dayOfYear(b.day)
                        .startOf('day')
                    bF = moment()
                        .year(b.year)
                        .dayOfYear(b.day)
                        .endOf('day')
                } else if (a.week) {
                    aS = moment()
                        .year(a.year)
                        .week(a.week)
                        .startOf('week')
                    aF = moment()
                        .year(a.year)
                        .week(a.week)
                        .endOf('week')
                    bS = moment()
                        .year(b.year)
                        .week(b.week)
                        .startOf('week')
                    bF = moment()
                        .year(b.year)
                        .week(b.week)
                        .endOf('week')
                } else if (a.month) {
                    aS = moment()
                        .year(a.year)
                        .month(a.month)
                        .startOf('month')
                    aF = moment()
                        .year(a.year)
                        .month(a.month)
                        .endOf('month')
                    bS = moment()
                        .year(b.year)
                        .month(b.month)
                        .startOf('month')
                    bF = moment()
                        .year(b.year)
                        .month(b.month)
                        .endOf('month')
                } else if (a.year) {
                    aS = moment()
                        .year(a.year)
                        .startOf('year')
                    aF = moment()
                        .year(a.year)
                        .endOf('year')
                    bS = moment()
                        .year(b.year)
                        .startOf('year')
                    bF = moment()
                        .year(b.year)
                        .endOf('year')
                }
                if (aS) {
                    const now = moment()

                    if (
                        Math.min(
                            Math.abs(now.diff(aS)),
                            Math.abs(now.diff(aF))
                        ) <=
                        Math.min(Math.abs(now.diff(bS)), Math.abs(now.diff(bF)))
                    )
                        return -1
                    else return 1
                }
            }
            return 0
        })

    const nowStageId = lastStages.length > 0 ? lastStages[0].stageId : 0

    lastStages = progress.stages

    return lastStages.length === 0 ? null : (
        <div
            style={{
                border: '1px solid #eee',
                padding: '10px 5px',
            }}
        >
            <Tabs
                type="card"
                defaultActiveKey={
                    nowStageId
                        ? lastStages
                              .findIndex(item => item.stageId === nowStageId)
                              .toString()
                        : '2'
                }
                // className={classes.fff}
            >
                {lastStages.map((stage, index) => {
                    const getStatus = id => {
                        if (isOwner && stage.dismissed) return 'dismissed'
                        if (stage.failBy.find(item => item.accountId === id))
                            return 'fail'
                        if (
                            stage.approvedBy.find(item => item.accountId === id)
                        )
                            return 'complete'
                        return 'process'
                    }

                    const getTag = id => {
                        const status = getStatus(id)
                        let text = 'In process',
                            color = '#108ee9'
                        switch (status) {
                            case 'fail':
                                text = 'Failed'
                                color = '#f50'
                                break
                            case 'complete':
                                text = 'Complete'
                                color = '#87d068'
                                break
                            case 'dismissed':
                                text = 'Dismissed'
                                color = '#ffa500'
                                break
                            default:
                                break
                        }

                        return <Tag color={color}>{text}</Tag>
                    }

                    const experts = goal.experts.map(expert => (
                        <Descriptions.Item
                            key={expert}
                            label={
                                <>
                                    <UserButton
                                        id={expert}
                                        noAva={true}
                                        span={true}
                                    />{' '}
                                    {' (expert)'}
                                </>
                            }
                        >
                            {getTag(expert)}
                        </Descriptions.Item>
                    ))

                    const users = goal.users.map(user => (
                        <Descriptions.Item
                            key={user}
                            label={
                                <>
                                    <UserButton
                                        id={user}
                                        noAva={true}
                                        span={true}
                                    />{' '}
                                    {' (achiever)'}
                                </>
                            }
                        >
                            {getTag(user)}
                        </Descriptions.Item>
                    ))

                    const owner = [
                        <Descriptions.Item
                            key={progress.owner}
                            label={
                                <>
                                    <UserButton
                                        id={progress.owner}
                                        noAva={true}
                                        span={true}
                                    />{' '}
                                    {' (owner)'}
                                </>
                            }
                        >
                            {getTag(progress.owner)}
                        </Descriptions.Item>,
                    ]
                    let stageDate
                    let tabTitle = ''
                    if (stage.day) {
                        stageDate = moment()
                            .year(stage.year)
                            .dayOfYear(stage.day)
                        tabTitle = (
                            <>
                                <div>{stageDate.format('dddd')}</div>
                                <div>{stageDate.format('D MMM YY')}</div>
                            </>
                        )
                    } else if (stage.week) {
                        stageDate = moment()
                            .year(stage.year)
                            .week(stage.week)
                        tabTitle = (
                            <>
                                <div>{stageDate.format('wo')} week</div>
                                <div>
                                    {stageDate
                                        .startOf('week')
                                        .format('D MMM YY')}
                                </div>
                                <div>
                                    {stageDate.endOf('week').format('D MMM YY')}
                                </div>
                            </>
                        )
                    } else if (stage.month) {
                        stageDate = moment()
                            .year(stage.year)
                            .month(stage.month)
                        tabTitle = (
                            <>
                                <div>{stageDate.format('MMMM')}</div>
                                <div>{stageDate.format('YYYY')}</div>
                            </>
                        )
                    } else if (stage.year) {
                        stageDate = moment().year(stage.year)

                        tabTitle = <div>{stageDate.format('YYYY')}</div>
                    }

                    const myStatus =
                        account && account._id && getStatus(account._id)

                    const stageStatus = getStatus(progress.owner)
                    return (
                        <Tabs.TabPane
                            tab={
                                <Badge
                                    color={
                                        stageStatus === 'complete'
                                            ? 'green'
                                            : stageStatus === 'dismissed'
                                            ? 'orange'
                                            : stageStatus === 'fail'
                                            ? 'volcano'
                                            : 'rgba(0,0,0,0)'
                                    }
                                >
                                    <span
                                        style={{
                                            fontSize: '12px',
                                            textTransform: 'uppercase',
                                            textAlign: 'center',
                                        }}
                                    >
                                        {tabTitle}
                                    </span>
                                </Badge>
                            }
                            key={index}
                        >
                            {account && account._id && (
                                <>
                                    Set status:
                                    <Radio.Group
                                        value={myStatus}
                                        style={{ padding: '20px 0px ' }}
                                        onChange={value =>
                                            wsActions.changeStage(
                                                value.target.value,
                                                account._id,
                                                progress._id,
                                                stage.stageId
                                            )
                                        }
                                    >
                                        <Radio value="process">
                                            <Tag
                                                color={
                                                    myStatus !== 'process'
                                                        ? 'processing'
                                                        : '#108ee9'
                                                }
                                            >
                                                In process
                                            </Tag>
                                        </Radio>
                                        <Radio value="complete">
                                            <Tag
                                                color={
                                                    myStatus !== 'complete'
                                                        ? 'green'
                                                        : '#87d068'
                                                }
                                            >
                                                Complete
                                            </Tag>
                                        </Radio>
                                        <Radio value="fail">
                                            <Tag
                                                color={
                                                    myStatus !== 'fail'
                                                        ? 'volcano'
                                                        : '#f50'
                                                }
                                            >
                                                Fail
                                            </Tag>
                                        </Radio>
                                        {isOwner && (
                                            <Radio value="dismissed">
                                                <Tag
                                                    color={
                                                        myStatus !== 'dismissed'
                                                            ? 'orange'
                                                            : '#ffa500'
                                                    }
                                                >
                                                    Dismiss
                                                </Tag>
                                            </Radio>
                                        )}
                                    </Radio.Group>
                                </>
                            )}

                            <Descriptions
                                size="small"
                                bordered
                                column={{
                                    xxl: 4,
                                    xl: 3,
                                    lg: 3,
                                    md: 3,
                                    sm: 2,
                                    xs: 1,
                                }}
                            >
                                {owner}
                                {users}
                                {experts}
                            </Descriptions>

                            {stage.paid.length > 0 && (
                                <>
                                    <Divider style={{ fontSize: '15px' }}>
                                        Paid rewards
                                    </Divider>

                                    <Rewards
                                        rewardKey={1}
                                        rewards={stage.paid}
                                        readOnly={true}
                                    />
                                </>
                            )}
                        </Tabs.TabPane>
                    )
                })}
            </Tabs>
        </div>
    )
}
export default ActivityTable
