import React from 'react'

import * as wsActions from '../websocketActions'
import Menu from 'antd/es/menu'
import Dropdown from 'antd/es/dropdown'
import DownOutlined from '@ant-design/icons/DownOutlined'
import Button from 'antd/es/button'
import MoreIcon from './UI/MoreIcon'

const FriendActionButton = props => {
    const { invitingFriends, invitedFriends, realFriends, _id, account } = props
    const accountId = account ? account._id : ''
    if (!accountId) return []

    if (accountId === _id) return [props.additionalButton]

    const menu = (
        <Menu onClick={({ domEvent }) => domEvent.stopPropagation()}>
            {props.additionalButtons &&
                props.additionalButtons.map((button, index) => (
                    <Menu.Item key={index + 100} onClick={button.onClick}>
                        {button.title}
                    </Menu.Item>
                ))}
            {invitingFriends.find(item => item.friend === _id) ? (
                <Menu.Item
                    key="2"
                    onClick={() => wsActions.unfriend(accountId, _id)}
                    danger={true}
                >
                    Decline
                </Menu.Item>
            ) : invitedFriends.find(item => item.friend === _id) ? (
                <Menu.Item
                    key="3"
                    onClick={() => wsActions.unfriend(accountId, _id)}
                    danger={true}
                >
                    Cancel invitation
                </Menu.Item>
            ) : realFriends.find(item => item.friend === _id) ? (
                <Menu.Item
                    key="4"
                    onClick={() => wsActions.unfriend(accountId, _id)}
                    danger={true}
                >
                    Unfriend
                </Menu.Item>
            ) : null}
            {props.noFollow ? null : account.followAccounts.includes(_id) ? (
                <Menu.Item
                    key="5"
                    onClick={() => wsActions.unfollowAccount(accountId, _id)}
                    danger={true}
                >
                    Unfollow
                </Menu.Item>
            ) : (
                <Menu.Item
                    key="6"
                    onClick={() => wsActions.followAccount(accountId, _id)}
                >
                    Follow
                </Menu.Item>
            )}
        </Menu>
    )
    return invitingFriends.find(item => item.friend === _id) ? (
        <Dropdown.Button
            type="primary"
            overlay={menu}
            trigger={['click', 'hover']}
            onClick={e => {
                e.preventDefault()
                e.stopPropagation()
                wsActions.acceptFriend(accountId, _id)
            }}
        >
            Accept friendship
        </Dropdown.Button>
    ) : ![...realFriends, ...invitingFriends, ...invitedFriends].find(
          item => item.friend === _id
      ) ? (
        <Dropdown.Button
            type="primary"
            overlay={menu}
            trigger={['click', 'hover']}
            onClick={e => {
                e.preventDefault()
                e.stopPropagation()
                wsActions.addFriend(accountId, _id)
            }}
        >
            Become friends
        </Dropdown.Button>
    ) : (
        <Dropdown overlay={menu} trigger={['click']}>
            <a
                // className="ant-dropdown-link"
                onClick={e => {
                    e.preventDefault()
                    e.stopPropagation()
                }}
            >
                <Button type={props.asText ? 'primary' : 'link'}>
                    {props.asText ? (
                        <>
                            Actions <DownOutlined />
                        </>
                    ) : (
                        <MoreIcon />
                    )}
                </Button>
            </a>
        </Dropdown>
    )
}

export default FriendActionButton
