import { webSocket } from '../containers/ConnectWebsocket/ConnectWebsocket'

export const changeStage = (value, accountId, progressId, stageId) => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'changeStage',
                value,
                progressId,
                accountId,
                stageId,
            })
        )
    }
}

export const leaveProgress = data => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'leaveProgress',
                progressId: data.progress._id,
                accountId: data.account._id,
            })
        )
    }
}

export const getFriendsData = (progress, accountFriends) => {
    if (webSocket && webSocket.readyState === 1) {
        if (progress) {
            let accountIds = [
                progress.owner,
                progress.worker,
                ...progress.goal.supporters,
                ...progress.goal.experts,
                ...accountFriends,
            ]

            accountIds = [...new Set(accountIds)]

            webSocket.send(
                JSON.stringify({
                    messageCode: 'getFriendsData',
                    accountIds,
                })
            )
        }
    }
}

