import React from 'react'
import { connect } from 'react-redux'
import { withRouter, Link } from 'react-router-dom'
import UserButton from '../../components/UserButton/UserButton'
import MasonryGrid from '../../components/MasonryGrid'
import MasonryCard from '../../components/MasonryCard'

const Wallet = props => {
    const account = props.pulledProfile || props.account
    return account.wallet ? (
        <MasonryGrid friends={true}>
            {account.wallet.map((money, index) => (
                <Link to={'/profile/' + money.user} key={index}>
                    <MasonryCard
                        body={
                            <>
                                <span style={{ fontWeight: 'bold' }}>
                                    ${money.amount}
                                </span>
                                <UserButton span={true} id={money.user} />
                            </>
                        }
                    />
                </Link>
            ))}
        </MasonryGrid>
    ) : null
}
const mapStateToProps = state => {
    return {
        account: state.account,
        isAuthed: state.isAuthed,
    }
}

export default withRouter(connect(mapStateToProps)(Wallet))
