import { webSocket } from '../containers/ConnectWebsocket/ConnectWebsocket'

export const savePerk = (accountId, id, value, mode) => {
    if (accountId && webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode:
                    mode === 'wishlist'
                        ? 'saveWishlistItem'
                        : mode === 'goals'
                        ? 'saveGoal'
                        : 'savePerk',
                id,
                value,
                accountId,
            })
        )
    }
}

export const deletePerk = (accountId, id, mode) => {
    if (accountId && id && webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode:
                    mode === 'wishlist'
                        ? 'deleteWishlistItem'
                        : mode === 'goals'
                        ? 'deleteGoal'
                        : 'deletePerk',
                id,
                accountId,
            })
        )
    }
}

export const buyPerk = (buyer, seller, perkId) => {
    if (perkId && buyer && seller && webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'buyPerk',
                perkId,
                buyer,
                seller,
            })
        )
    }
}
