import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import AddCard from '../../components/AddCard/AddCard'
import Modal from 'antd/es/modal/Modal'
import AddProgress from './AddProgress'
import MasonryGrid from '../../components/MasonryGrid'
import MasonryCard from '../../components/MasonryCard'
import UserButton from '../../components/UserButton/UserButton'
import Helmet from 'react-helmet'

const Progresses = props => {
    const [startProgressVisible, setStartProgressVisible] = useState(false)

    const { fromProfile, progressData, account, profile } = props

    const { progresses } = fromProfile ? profile : account
    if (!fromProfile) props.setPageTitle({ main: 'Inspirations', sub: '' })
    let loadedProgresses
    if (progresses) {
        loadedProgresses = progresses
            .filter(item => progressData[item])
            .map(item => progressData[item])
    }

    const editGoal = fromProfile ? null : loadedProgresses ? (
        <Modal
            title="Add progress"
            visible={startProgressVisible}
            maskClosable
            closable
            onCancel={() => setStartProgressVisible(false)}
            width="100%"
            style={{ maxWidth: '1000px' }}
            footer={null}
            destroyOnClose={true}
        >
            <AddProgress
                setStartProgressVisible={setStartProgressVisible}
                account={account}
                friendData={props.friendData}
                cancel={() => setStartProgressVisible(false)}
            />
        </Modal>
    ) : null

    return loadedProgresses ? (
        <div>
            <Helmet>
                <title>Addspire - Your inspirations live here!</title>
                <meta
                    name="description"
                    content="Addspire is a social network where you can inspire and motivate yourself or the people you care."
                />
            </Helmet>
            {!fromProfile && (
                <>
                    <AddCard
                        title="Start a new progress"
                        sub="Rich your goals or help others!"
                        action={() => {
                            setStartProgressVisible(true)
                        }}
                    />
                    {editGoal}
                </>
            )}
            <MasonryGrid style={{ marginTop: '40px' }}>
                {loadedProgresses.map((item, index) => (
                    <MasonryCard
                        key={index}
                        onClickLink={'/inspirations/' + item._id}
                        images={item.goal.images}
                        body={
                            <>
                                <UserButton
                                    ids={item.goal.users}
                                    group={true}
                                />
                                <span style={{ fontWeight: 'bold' }}>
                                    {item.name}
                                </span>
                            </>
                        }
                    />
                ))}
            </MasonryGrid>
        </div>
    ) : null
}

const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
        isAuthed: state.isAuthed,
        progressData: state.progressData,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: (data, pulledFrom) =>
            dispatch(actions.setData(data, pulledFrom)),
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        tryAuth: (props, forceAuth) =>
            dispatch(actions.tryAuth(props, forceAuth)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Progresses)
)
