export default props => {
    const createEmptyObj = () => ({
        user: [],
        account: [],
        progress: [],
        post: [],
        progressData: [],
        friendData: [],
        postData: [],
        transactionData: [],
    })
    const { store } = props
    const requestedResources = createEmptyObj()
    const neededResources = createEmptyObj()
    if (store.isAuthed) {
        if (!store.user) {
            requestedResources.user.push(store.isAuthed)
        } else {
            neededResources.user.push(store.user._id)
            if (store.currentAccount) {
                if (store.account._id !== store.currentAccount) {
                    requestedResources.account.push(store.currentAccount)
                } else {
                    neededResources.account.push(store.account._id)
                    for (let progress of [
                        ...store.account.progresses,
                        ...store.account.followProgresses,
                    ]) {
                        if (!store.progressData[progress]) {
                            requestedResources.progressData.push(progress)
                        }
                        neededResources.progressData.push(progress)
                    }

                    for (let post of store.account.followPosts) {
                        if (!store.postData[post]) {
                            requestedResources.postData.push(post)
                        }
                        neededResources.postData.push(post)
                    }
                    for (let post of store.account.myPosts) {
                        if (!store.postData[post]) {
                            requestedResources.postData.push(post)
                        }
                        neededResources.postData.push(post)
                    }
                    for (let transaction of store.account.transactions) {
                        if (!store.transactionData[transaction]) {
                            requestedResources.transactionData.push(transaction)
                        }
                        neededResources.transactionData.push(transaction)
                    }
                    for (let friend of store.account.friends) {
                        if (!store.friendData[friend.friend]) {
                            requestedResources.friendData.push(friend.friend)
                        }
                        neededResources.friendData.push(friend.friend)
                    }
                    for (let item of [
                        ...store.account.followingAccounts,
                        ...store.account.followAccounts,
                    ]) {
                        if (!store.friendData[item]) {
                            requestedResources.friendData.push(item)
                        }
                        neededResources.friendData.push(item)
                    }
                }
            }
            if (store.currentProfile) {
                if (store.profile._id !== store.currentProfile) {
                    requestedResources.account.push(store.currentProfile)
                } else {
                    neededResources.account.push(store.profile._id)
                    for (let progress of [
                        ...store.profile.progresses,
                        ...store.profile.followProgresses,
                    ]) {
                        if (!store.progressData[progress]) {
                            requestedResources.progressData.push(progress)
                        }
                        neededResources.progressData.push(progress)
                    }

                    for (let friend of store.profile.friends) {
                        if (!store.friendData[friend.friend]) {
                            requestedResources.friendData.push(friend.friend)
                        }
                        neededResources.friendData.push(friend.friend)
                    }
                    for (let item of [
                        ...store.profile.followingAccounts,
                        ...store.profile.followAccounts,
                    ]) {
                        if (!store.friendData[item]) {
                            requestedResources.friendData.push(item)
                        }
                        neededResources.friendData.push(item)
                    }
                }
            }

            if (store.currentProgress) {
                if (store.progress._id !== store.currentProgress) {
                    requestedResources.progress.push(store.currentProgress)
                } else {
                    neededResources.progress.push(store.progress._id)
                    let participants = []
                    if (store.progress.owner)
                        participants.push(store.progress.owner)
                    if (store.progress.worker)
                        participants.push(store.progress.worker)
                    participants = [
                        ...participants,
                        ...(store.progress.experts || []),
                        ...(store.progress.supporters || []),
                    ]
                    for (let friend of participants) {
                        if (!store.friendData[friend]) {
                            requestedResources.friendData.push(friend)
                        }
                        neededResources.friendData.push(friend)
                    }
                    for (let post of store.progress.posts) {
                        if (!store.postData[post]) {
                            requestedResources.postData.push(post)
                        }
                        neededResources.postData.push(post)
                    }
                }
            }
            if (store.currentPosts) {
                for (let post of store.currentPosts) {
                    const postEl = store.post[post]
                    if (!postEl) {
                        requestedResources.post.push(post)
                    } else {
                        neededResources.post.push(post)
                        for (let friend of postEl.users) {
                            if (!store.friendData[friend]) {
                                requestedResources.friendData.push(friend)
                            }
                            neededResources.friendData.push(friend)
                        }
                        if (post.parent) {
                            if (post.parent.parentType === 'progress')
                                neededResources.progressData.push(
                                    post.parent.parentId
                                )
                        }
                    }
                }
            }
        }
    }

    const reduce = obj => {
        for (let attr in obj) {
            if (obj[attr]) {
                obj[attr] = Array.from(new Set(obj[attr]))
            }
        }
    }

    reduce(neededResources)
    const notNeededResources = createEmptyObj()
    notNeededResources.profile = []
    let notNeededExist
    const check1 = (key, mode) => {
        if (
            store[mode]._id &&
            !neededResources[key].includes(store[mode]._id)
        ) {
            notNeededResources[mode].push(store[mode]._id)
            notNeededExist = true
        }
    }
    const check2 = key => {
        for (let attr in store[key]) {
            if (!neededResources[key].includes(attr)) {
                notNeededResources[key].push(attr)
                notNeededExist = true
            }
        }
    }
    check1('account', 'account')
    check1('account', 'profile')
    check1('progress', 'progress')
    check2('post')
    check2('friendData')
    check2('progressData')
    check2('transactionData')
    check2('postData')

    if (notNeededExist) {
        props.removeData(notNeededResources)
    }

    reduce(requestedResources)

    return requestedResources
}
