import React, { useState } from 'react'
import Modal from 'antd/es/modal'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as wsActions from '../../websocketActions'
import Button from 'antd/es/button'
import Input from 'antd/es/input'
import InputNumber from 'antd/es/input-number'
import Form from 'antd/es/form'
import Select from 'antd/es/select'

import Upload from 'antd/es/upload'
import Divider from 'antd/es/divider'
import PlusOutlined from '@ant-design/icons/PlusOutlined'

import { uploadFile } from '../../utils/uploadFile'
import {
    descriptionRules,
    nameRules,
    priceRules,
} from '../../constants/fieldRules'
import connectionsSelect from '../EditGoalAndProgressForm/connectionsSelect'
import Descriptions from 'antd/es/descriptions'
import UserButton from '../UserButton/UserButton'
import Dropdown from 'antd/es/dropdown'
import Menu from 'antd/es/menu'
import Card from 'antd/es/card'
import MoreIcon from '../UI/MoreIcon'

const Rewards = props => {
    const [previewImage, setPreviewImage] = useState('')
    const [previewReward, setPreviewReward] = useState()
    const [editedReward, setEditedReward] = useState()
    const [editedRewardIndex, setEditedRewardIndex] = useState()
    const { account, friendData, readOnly, progress } = props

    const RewardButton = props => (
        <Button type="link" onClick={() => setPreviewReward(props.item)}>
            {props.item.mode === 'simple'
                ? props.item.simple
                : props.item.mode === 'money'
                ? props.item.money + ' (money)'
                : props.item.mode === 'item'
                ? props.item.itemName
                : ''}
        </Button>
    )

    return props.rewards ? (
        <div>
            {props.onlyButton ? (
                props.rewards.map((item, index) => (
                    <RewardButton item={item} key={index} />
                ))
            ) : (
                <Form.Item label={props.label} key={props.rewardKey}>
                    {props.rewards.length > 0 && (
                        <ol>
                            {props.rewards.map((item, index) => {
                                return (
                                    <li key={index}>
                                        <RewardButton item={item} />
                                        <UserButton
                                            id={item.owner}
                                            noName={true}
                                        />{' '}
                                        in
                                        {item.for && item.for.length > 0 ? (
                                            <UserButton
                                                id={item.for}
                                                noName={true}
                                            />
                                        ) : (
                                            <span
                                                style={{
                                                    fontWeight: 'bold',
                                                    marginLeft: '5px',
                                                }}
                                            >
                                                All
                                            </span>
                                        )}
                                        {item.owner === account._id &&
                                            !readOnly && (
                                                <>
                                                    <Dropdown
                                                        overlay={
                                                            <Menu>
                                                                <Menu.Item
                                                                    onClick={() => {
                                                                        setEditedRewardIndex(
                                                                            index
                                                                        )
                                                                        setEditedReward(
                                                                            item
                                                                        )
                                                                    }}
                                                                >
                                                                    Edit
                                                                </Menu.Item>

                                                                <Menu.Item
                                                                    onClick={() => {
                                                                        if (
                                                                            !window.confirm(
                                                                                'Are you sure you want to delete this item?'
                                                                            )
                                                                        )
                                                                            return
                                                                        if (
                                                                            props.setRewards
                                                                        ) {
                                                                            props.setRewards(
                                                                                props.rewards.filter(
                                                                                    (
                                                                                        item,
                                                                                        indexIn
                                                                                    ) =>
                                                                                        indexIn !==
                                                                                        index
                                                                                )
                                                                            )
                                                                        } else {
                                                                            wsActions.deleteReward(
                                                                                account._id,
                                                                                progress._id,
                                                                                item.rewardId
                                                                            )
                                                                        }
                                                                    }}
                                                                >
                                                                    Delete
                                                                </Menu.Item>
                                                            </Menu>
                                                        }
                                                    >
                                                        <Button type="link">
                                                            <MoreIcon />
                                                        </Button>
                                                    </Dropdown>
                                                </>
                                            )}
                                    </li>
                                )
                            })}
                        </ol>
                    )}

                    {!readOnly && (
                        <Button
                            type="link"
                            style={{ paddingLeft: 0 }}
                            onClick={() => {
                                setEditedRewardIndex('new')
                                setEditedReward({ mode: 'simple' })
                            }}
                        >
                            <PlusOutlined />
                            Add rewards
                        </Button>
                    )}
                </Form.Item>
            )}
            <Modal
                visible={editedReward}
                title={
                    editedReward === 'new' ? 'Add new reward' : 'Edit reward'
                }
                closable={true}
                destroyOnClose={true}
                onCancel={() => setEditedReward()}
                onOk={() => {
                    let rewardToSave = {}
                    if (editedReward.mode === 'money') {
                        rewardToSave.money = editedReward.money
                        rewardToSave.mode = editedReward.mode
                        rewardToSave.for = editedReward.for
                        rewardToSave.rewardId = editedReward.rewardId
                    } else {
                        rewardToSave = { ...editedReward, money: undefined }
                    }
                    rewardToSave.owner = account._id

                    if (props.setRewards) {
                        let newRewards = [...props.rewards]

                        if (editedRewardIndex === 'new')
                            newRewards = [...newRewards, rewardToSave]
                        else
                            newRewards = newRewards.map((item, index) => {
                                if (index === editedRewardIndex)
                                    return rewardToSave
                                else return item
                            })
                        props.setRewards(newRewards)
                        setEditedReward()
                        setEditedRewardIndex()
                    } else {
                        wsActions.saveReward(
                            account._id,
                            progress._id,
                            props.rewardKey,
                            rewardToSave
                        )
                        setEditedReward()
                        setEditedRewardIndex()
                    }
                }}
                destroyOnClose={true}
                okText="Save"
            >
                {editedReward && (
                    <>
                        <Form.Item label="Reward type">
                            <Select
                                placeholder="Type"
                                value={editedReward.mode}
                                onSelect={value => {
                                    setEditedReward({
                                        ...editedReward,
                                        mode: value,
                                    })
                                }}
                                style={{ width: '100%' }}
                                getPopupContainer={trigger =>
                                    trigger.parentNode
                                }
                            >
                                <Select.Option key="simple" value="simple">
                                    Simple reward
                                </Select.Option>
                                <Select.Option key="item" value="item">
                                    Item reward
                                </Select.Option>
                                <Select.Option key="money" value="money">
                                    Money reward
                                </Select.Option>
                            </Select>
                        </Form.Item>
                        <Form.Item label="Reward for">
                            {connectionsSelect(
                                account,
                                friendData,
                                editedReward.for,
                                value => {
                                    setEditedReward({
                                        ...editedReward,
                                        for: value,
                                    })
                                }
                            )}
                            <p>(leave blank for all)</p>
                        </Form.Item>
                        <Divider type="horizontal" />
                        {editedReward.mode === 'simple' ? (
                            <Form.Item
                                label="Description"
                                rules={[
                                    ...descriptionRules,
                                    {
                                        required: true,
                                        min: 1,
                                        whitespace: true,
                                        message:
                                            'Desription is too short. Minimal length is 1.',
                                    },
                                ]}
                            >
                                <Input.TextArea
                                    value={editedReward.simple}
                                    onChange={({ target: { value } }) =>
                                        setEditedReward({
                                            ...editedReward,
                                            simple: value,
                                        })
                                    }
                                    placeholder="Explain how you will reward"
                                />
                            </Form.Item>
                        ) : editedReward.mode === 'money' ? (
                            <Form.Item label="Amount" rules={priceRules}>
                                <InputNumber
                                    value={editedReward.money}
                                    onChange={value => {
                                        setEditedReward({
                                            ...editedReward,
                                            money: value,
                                        })
                                    }}
                                />
                            </Form.Item>
                        ) : (
                            <>
                                <Form.Item label="Images">
                                    <Upload
                                        listType="picture-card"
                                        fileList={(
                                            editedReward.itemImages || []
                                        ).map((url, index) => ({
                                            url,
                                            uid: index,
                                        }))}
                                        onPreview={file => {
                                            setPreviewImage(file.url)
                                        }}
                                        onChange={({ fileList }) => {
                                            setEditedReward({
                                                ...editedReward,
                                                itemImages: fileList.map(
                                                    image => image.url
                                                ),
                                            })
                                        }}
                                        customRequest={({ file }) => {
                                            uploadFile(
                                                file,
                                                800,
                                                undefined,
                                                url => {
                                                    setEditedReward({
                                                        ...editedReward,
                                                        itemImages: [
                                                            ...(editedReward.itemImages ||
                                                                []),
                                                            url,
                                                        ],
                                                    })
                                                }
                                            )
                                        }}
                                    >
                                        {(editedReward.itemImages || [])
                                            .length >= 8 ? null : (
                                            <div>
                                                <PlusOutlined />
                                                <div className="ant-upload-text">
                                                    Upload
                                                </div>
                                            </div>
                                        )}
                                    </Upload>
                                </Form.Item>
                                <Form.Item label="Name" rules={nameRules}>
                                    <Input
                                        value={editedReward.itemName}
                                        onChange={({ target: { value } }) =>
                                            setEditedReward({
                                                ...editedReward,
                                                itemName: value,
                                            })
                                        }
                                    />
                                </Form.Item>
                                <Form.Item
                                    label="Description"
                                    rules={descriptionRules}
                                >
                                    <Input.TextArea
                                        value={editedReward.itemDescription}
                                        onChange={({ target: { value } }) =>
                                            setEditedReward({
                                                ...editedReward,
                                                itemDescription: value,
                                            })
                                        }
                                    />
                                </Form.Item>
                            </>
                        )}
                    </>
                )}
            </Modal>
            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => setPreviewImage('')}
                destroyOnClose={true}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
            <Modal
                visible={previewReward}
                footer={null}
                onCancel={() => setPreviewReward()}
                title="Reward details"
                destroyOnClose={true}
            >
                {previewReward ? (
                    <>
                        {previewReward.itemImages &&
                            previewReward.itemImages.map((image, index) => (
                                <span
                                    key={index}
                                    onClick={() => setPreviewImage(image)}
                                >
                                    <Card
                                        type="inner"
                                        style={{
                                            width: 200,
                                            display: 'inline-block',
                                        }}
                                        hoverable
                                        cover={
                                            <div
                                                style={{
                                                    backgroundImage:
                                                        'url(' + image + ')',
                                                    height: 200,
                                                    width: 200,
                                                    backgroundSize: 'cover',
                                                }}
                                            />
                                        }
                                    />
                                </span>
                            ))}

                        <Descriptions
                            title="Reward description"
                            bordered
                            column={{
                                xxl: 4,
                                xl: 3,
                                lg: 3,
                                md: 3,
                                sm: 2,
                                xs: 1,
                            }}
                        >
                            {previewReward.mode === 'item' && (
                                <Descriptions.Item label="Name">
                                    {previewReward.itemName}
                                </Descriptions.Item>
                            )}
                            {previewReward.mode === 'item' && (
                                <Descriptions.Item label="Description">
                                    <p style={{ whiteSpace: 'pre-wrap' }}>
                                        {previewReward.itemDescription}
                                    </p>
                                </Descriptions.Item>
                            )}
                            {previewReward.mode === 'simple' && (
                                <Descriptions.Item label="Description">
                                    <p style={{ whiteSpace: 'pre-wrap' }}>
                                        {previewReward.simple}
                                    </p>
                                </Descriptions.Item>
                            )}
                            {previewReward.mode === 'money' && (
                                <Descriptions.Item label="Money">
                                    {previewReward.money}
                                </Descriptions.Item>
                            )}
                        </Descriptions>
                    </>
                ) : null}
            </Modal>
        </div>
    ) : null
}

const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        friendData: state.friendData,
        progress: state.progress,
    }
}

export default withRouter(connect(mapStateToProps)(Rewards))
