import React, { useState } from 'react'
import Modal from 'antd/es/modal'

import Button from 'antd/es/button'
import Input from 'antd/es/input'
import InputNumber from 'antd/es/input-number'
import Form from 'antd/es/form'
import Select from 'antd/es/select'
import Upload from 'antd/es/upload'
import PlusOutlined from '@ant-design/icons/PlusOutlined'

import Avatar from 'antd/es/avatar'
import Popconfirm from 'antd/es/popconfirm'
import {
    nameRules,
    descriptionRules,
    priceRules,
} from '../../constants/fieldRules'
import getAvatar from '../../utils/getAvatar'

const AddPerkForm = props => {
    const [previewImage, setPreviewImage] = useState('')
    const {
        connections,
        perkId,
        perk,
        deletePerk,
        onFinish,
        images,
        setImages,
        uploadImage,
    } = props

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }
    const tailLayout = {
        wrapperCol: { offset: 8, span: 16 },
    }

    const connectionsSelect = (
        <Select
            mode="multiple"
            style={{ width: '100%' }}
            placeholder="Please select"
            getPopupContainer={trigger => trigger.parentNode}
        >
            {connections.map(item => (
                <Select.Option key={item._id}>
                    <Avatar
                        src={getAvatar(item._id, item.image)}
                        size={20}
                        style={{ marginRight: '10px' }}
                    />
                    {item.name} - ({item._id})
                </Select.Option>
            ))}
        </Select>
    )
    const [form] = Form.useForm()

    const normFile = e => {
        if (Array.isArray(e)) {
            return e
        }
        return e && e.fileList.map(image => image.url)
    }

    return !props.perkId || perk ? (
        <>
            <Form
                form={form}
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                initialValues={perk || {}}
                {...layout}
            >
                <Form.Item
                    name="images"
                    label="Images"
                    valuePropName="images"
                    getValueFromEvent={normFile}
                >
                    <Upload
                        listType="picture-card"
                        fileList={images.map((image, index) => ({
                            ...image,
                            uid: index,
                        }))}
                        onPreview={file => {
                            setPreviewImage(file.url)
                        }}
                        onChange={({ fileList }) => setImages(fileList)}
                        customRequest={({ file }) => {
                            uploadImage(file)
                        }}
                    >
                        {images.length >= 8 ? null : (
                            <div>
                                <PlusOutlined />
                                <div className="ant-upload-text">Upload</div>
                            </div>
                        )}
                    </Upload>
                </Form.Item>
                <Form.Item label="Name" name="name" rules={nameRules}>
                    <Input placeholder="New item" />
                </Form.Item>
                <Form.Item
                    label="Description"
                    name="description"
                    rules={descriptionRules}
                >
                    <Input.TextArea placeholder="Write a few words about this item." />
                </Form.Item>

                {props.mode === 'shop' && (
                    <Form.Item label="Available for" name="users">
                        {connectionsSelect}
                    </Form.Item>
                )}

                {props.mode === 'shop' && (
                    <Form.Item label="Price" name="price" rules={priceRules}>
                        <InputNumber />
                    </Form.Item>
                )}

                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit" size="large">
                        Save
                    </Button>

                    {perkId && (
                        <Popconfirm
                            title="Are you sure you want to delete this item?"
                            onConfirm={deletePerk}
                            onCancel={() => {}}
                            okText="Confirm"
                            cancelText="Cancel"
                        >
                            <Button type="link" danger size="large">
                                Delete
                            </Button>
                        </Popconfirm>
                    )}
                </Form.Item>
            </Form>
            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => setPreviewImage('')}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
        </>
    ) : null
}

export default AddPerkForm
