import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import Button from 'antd/es/button'
import Comment from 'antd/es/comment'
import Avatar from 'antd/es/avatar'
import Input from 'antd/es/input'
import Form from 'antd/es/form'
import Upload from 'antd/es/upload'
import PictureFilled from '@ant-design/icons/PictureFilled'
import LoadingOutlined from '@ant-design/icons/LoadingOutlined'
import CloseCircleFilled from '@ant-design/icons/CloseCircleFilled'
import SendOutlined from '@ant-design/icons/SendOutlined'
import { uploadFile } from '../../utils/uploadFile'
import * as wsActions from '../../websocketActions'
import getAvatar from '../../utils/getAvatar'
import { messageRules } from '../../constants/fieldRules'
import PlusOutlined from '@ant-design/icons/PlusOutlined'
import message from 'antd/es/message'
import Modal from 'antd/es/modal/Modal'

const AddMessage = props => {
    const [previewImage, setPreviewImage] = useState('')
    const [imageLoading, setImageLoading] = useState(false)
    const [images, setImages] = useState(props.images || [])
    const { post, account } = props

    const [messageText, setMessageText] = useState(
        props.content ||
            (props.replyToUser ? '@' + props.replyToUser + ' ' : '')
    )

    useEffect(() => {
        setMessageText(
            props.content ||
                (props.replyToUser ? '@' + props.replyToUser + ' ' : '')
        )
    }, [props.replyToUser, props.content])

    const uploadImage = file => {
        uploadFile(file, 800, undefined, url => {
            setImages([...images, url])
        })
    }

    return post && account._id ? (
        <div
            style={{
                background: '#fff',

                ...(props.replyTo
                    ? {}
                    : {
                          width: '100%',
                          bottom: '-200px',
                          left: '0px',
                          //   position: 'absolute',
                      }),
            }}
        >
            <Comment
                avatar={<Avatar src={getAvatar(account._id, account.image)} />}
                content={
                    <>
                        <Form.Item rules={messageRules} style={{ margin: 0 }}>
                            <Input.TextArea
                                autoSize={{
                                    minRows: 1,
                                    maxRows: 3,
                                }}
                                rows={1}
                                onChange={e => setMessageText(e.target.value)}
                                value={messageText}
                                placeholder="Write a comment"
                            />
                        </Form.Item>
                        {images.length > 0 && (
                            <Form.Item style={{ margin: 0 }}>
                                <Upload
                                    listType="picture-card"
                                    fileList={images.map((image, index) => ({
                                        url: image,
                                        uid: index,
                                    }))}
                                    onPreview={file => {
                                        setPreviewImage(file.url)
                                    }}
                                    onChange={({ fileList }) =>
                                        setImages(
                                            fileList.map(image => image.url)
                                        )
                                    }
                                    customRequest={({ file }) => {
                                        uploadImage(file)
                                    }}
                                >
                                    {images.length >= 8 ? null : (
                                        <div>
                                            <PlusOutlined />
                                            <div className="ant-upload-text">
                                                Upload
                                            </div>
                                        </div>
                                    )}
                                </Upload>
                            </Form.Item>
                        )}
                        <Form.Item style={{ margin: 0 }}>
                            <Button
                                htmlType="submit"
                                onClick={() => {
                                    wsActions.sendMessage(
                                        account._id,
                                        post._id,
                                        messageText,
                                        images,
                                        props.replyTo,
                                        props.editingMessage
                                    )
                                    setMessageText('')
                                    setImages([])
                                    if (props.onSend) props.onSend()
                                }}
                                icon={<SendOutlined />}
                                type="text"
                            >
                                Send
                            </Button>
                            <Upload
                                showUploadList={false}
                                customRequest={({ file }) => {
                                    if (images.length > 8) {
                                        message.warning(
                                            'You can not attach more than 8 images.'
                                        )
                                        return
                                    }
                                    setImageLoading(true)
                                    uploadFile(file, 800, undefined, url => {
                                        setImageLoading(false)
                                        setImages([...images, url])
                                    })
                                }}
                            >
                                <Button
                                    type="text"
                                    icon={
                                        imageLoading ? (
                                            <LoadingOutlined />
                                        ) : (
                                            <PictureFilled />
                                        )
                                    }
                                >
                                    Image
                                </Button>
                            </Upload>
                            {props.cancel && (
                                <Button
                                    onClick={props.cancel}
                                    icon={<CloseCircleFilled />}
                                    type="text"
                                >
                                    Cancel
                                </Button>
                            )}
                        </Form.Item>
                    </>
                }
            />
            <Modal
                visible={previewImage}
                footer={null}
                onCancel={() => setPreviewImage('')}
            >
                <img
                    alt="example"
                    style={{ width: '100%' }}
                    src={previewImage}
                />
            </Modal>
        </div>
    ) : null
}

export default withRouter(AddMessage)
