import React from 'react'
import { withRouter } from 'react-router-dom'

import * as wsActions from '../../websocketActions'
import Dropdown from 'antd/es/dropdown'
import Menu from 'antd/es/menu'
import Button from 'antd/es/button'
import MasonryCard from '../../components/MasonryCard'
import EditFilled from '@ant-design/icons/EditFilled'
import Card from 'antd/es/card'
import MoreIcon from '../../components/UI/MoreIcon'

const PerkCard = props => {
    const {
        perk,
        perkId,
        owner,
        account,
        setEditedPerk,
        setEditPerkOpen,
        mode,
    } = props

    return (
        <MasonryCard
            onClickLink={`/${mode || 'shop'}/${owner._id}/${perkId}`}
            images={perk.images}
            actions={
                <span onClick={e => e.stopPropagation()}>
                    {owner._id === account._id &&
                    !props.onlyBuy &&
                    props.mode === 'shop' ? (
                        <Dropdown.Button
                            type="primary"
                            key="1"
                            onClick={() =>
                                wsActions.buyPerk(
                                    account._id,
                                    owner._id,
                                    perkId
                                )
                            }
                            overlay={
                                <Menu>
                                    <Menu.Item
                                        key="0"
                                        onClick={() => {
                                            setEditedPerk(perkId)
                                            setEditPerkOpen(true)
                                        }}
                                    >
                                        Edit
                                    </Menu.Item>
                                </Menu>
                            }
                        >
                            Buy
                        </Dropdown.Button>
                    ) : props.mode === 'shop' ? (
                        <Button
                            type="primary"
                            onClick={() =>
                                wsActions.buyPerk(
                                    account._id,
                                    owner._id,
                                    perkId
                                )
                            }
                        >
                            Buy
                        </Button>
                    ) : owner._id === account._id && !props.onlyBuy ? (
                        <Dropdown
                            trigger={['click', 'hover']}
                            overlay={
                                <Menu>
                                    <Menu.Item
                                        key="0"
                                        onClick={() => {
                                            setEditedPerk(perkId)
                                            setEditPerkOpen(true)
                                        }}
                                    >
                                        Edit
                                    </Menu.Item>
                                </Menu>
                            }
                        >
                            <Button type="link">
                                <MoreIcon />
                            </Button>
                        </Dropdown>
                    ) : null}
                </span>
            }
            body={
                <span style={{ fontWeight: 'bold' }}>
                    {perk.name}
                    {mode === 'shop' && ` ($${perk.price})`}
                </span>
            }
        />
    )
}

export default withRouter(PerkCard)
