import React, { useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import WishlistAndPerks from '../Shop/WishlistAndPerks'
import Helmet from 'react-helmet'

const Wishlist = props => {
    useEffect(() => {
        props.setPageTitle({ main: 'Wishlist', sub: '' })
    }, [])

    return (
        <div>
            <Helmet>
                <title>Addspire - reward and motivate for great changes!</title>
                <meta
                    name="description"
                    content="Addspire is a social network where you can inspire and motivate yourself or the people you care."
                />
            </Helmet>
            {props.account._id ? (
                <WishlistAndPerks mode="wishlist" account={props.account} />
            ) : (
                <span />
            )}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Wishlist)
)
