import React from 'react'
import { withRouter } from 'react-router-dom'

import FriendCard from '../../components/FriendCard'
import MasonryGrid from '../../components/MasonryGrid'

const pendingFriends = props => {
    const { realFriends, invitedFriends, invitingFriends } = props
    return (
        <>
            {invitingFriends.length > 0 && (
                <>
                    <h2>People invited me to be friends</h2>
                    <MasonryGrid friends={true}>
                        {invitingFriends.map(friend => (
                            <FriendCard
                                key={friend._id}
                                {...props}
                                friend={friend}
                                realFriends={realFriends}
                                invitingFriends={invitingFriends}
                                invitedFriends={invitedFriends}
                            />
                        ))}
                    </MasonryGrid>
                </>
            )}

            {invitedFriends.length > 0 && (
                <>
                    <h2>I invited these people</h2>
                    <MasonryGrid friends={true}>
                        {invitedFriends.map(friend => (
                            <FriendCard
                                key={friend._id}
                                {...props}
                                friend={friend}
                                realFriends={realFriends}
                                invitingFriends={invitingFriends}
                                invitedFriends={invitedFriends}
                            />
                        ))}
                    </MasonryGrid>
                </>
            )}
        </>
    )
}

export default withRouter(pendingFriends)
