import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Tabs from 'antd/es/tabs'
import Wallet from './Wallet'
import Transactions from './Transactions'
import WishlistAndPerks from './WishlistAndPerks'
import Helmet from 'react-helmet'

const Shop = props => {
    useEffect(() => {
        props.setPageTitle({ main: 'Shop', sub: 'My items' })
    }, [])

    return props.account._id ? (
        <div>
            <Helmet>
                <title>Addspire - reward and motivate for great changes!</title>
                <meta
                    name="description"
                    content="Addspire is a social network where you can inspire and motivate yourself or the people you care."
                />
            </Helmet>
            <Tabs
                style={{
                    background: 'transparent',
                    boxShadow: 'none',
                    margin: '0',
                    marginTop: '-15px',
                }}
            >
                <Tabs.TabPane tab="My shop" key={1}>
                    <WishlistAndPerks
                        mode="shop"
                        listKey="perks"
                        account={props.account}
                    />
                </Tabs.TabPane>
                <Tabs.TabPane tab="Wallet" key={3}>
                    <Wallet />
                </Tabs.TabPane>
                <Tabs.TabPane tab="Transactions" key={4}>
                    <Transactions />
                </Tabs.TabPane>
            </Tabs>
        </div>
    ) : (
        <span />
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Shop)
