import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { uploadFile } from '../../utils/uploadFile'
import AddPerkForm from './AddPerkForm'
import { withRouter } from 'react-router-dom'
import * as wsActions from '../../websocketActions'

const AddPerk = props => {
    const [perk, setPerk] = useState()
    const [images, setImages] = useState([])
    const { account, mode } = props
    const perkId = props.perkId || ''
    useEffect(() => {
        if (perkId) {
            const perk =
                mode === 'wishlist'
                    ? account.wishlist.find(item => item.itemId === perkId)
                    : mode === 'goals'
                    ? account.goals.find(item => item.goalId === perkId)
                    : account.perks.find(item => item.perkId === perkId)
            if (perk) {
                setPerk(perk)
                setImages(
                    perk.images.map((url, index) => ({
                        url,
                        uid: index,
                    }))
                )
            }
        }
    }, [])

    const connections = [
        account,
        ...account.friends
            .filter(friend => friend.status === 'friend')
            .map(friend => props.friendData[friend.friend]),
    ]

    const uploadImage = file => {
        uploadFile(file, 800, undefined, url => {
            setImages(
                [...images, { url }].map((image, index) => ({
                    ...image,
                    uid: index,
                }))
            )
        })
    }

    return !props.perkId || perk ? (
        <AddPerkForm
            connections={connections}
            perkId={perkId}
            perk={perk}
            deletePerk={() => {
                wsActions.deletePerk(account._id, perkId, mode)
                props.setEditPerkOpen(false)
            }}
            onFinish={value => {
                const processedValue = {
                    ...value,
                    images: images.map(image => image.url),
                }
                wsActions.savePerk(account._id, perkId, processedValue, mode)
                props.setEditPerkOpen(false)
            }}
            images={images}
            setImages={setImages}
            uploadImage={uploadImage}
            mode={props.mode}
        />
    ) : null
}
const mapStateToProps = state => {
    return {
        account: state.account,
        friendData: state.friendData,
    }
}
export default withRouter(connect(mapStateToProps)(AddPerk))
