import React from 'react'
import { connect } from 'react-redux'
import * as actions from '../store/actions'
import { withRouter } from 'react-router-dom'

import friendActionButton from './FriendActionButton'
import UserButton from './UserButton/UserButton'
import MasonryCard from './MasonryCard'

const FriendCard = props => {
    const friendActions = friendActionButton({
        account: props.account,
        realFriends: props.realFriends,
        invitedFriends: props.invitedFriends,
        invitingFriends: props.invitingFriends,
        _id: props.friend._id,
        asIcons: true,

        additionalButtons: [
            {
                title: 'See profile',
                onClick: () =>
                    props.history.push('/profile/' + props.friend._id),
            },
        ],
    })

    return (
        <MasonryCard
            actions={friendActions}
            body={<UserButton span={true} id={props.friend._id} size={50} />}
            noLink={true}
        />
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
        isAuthed: state.isAuthed,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: (data, pulledFrom) =>
            dispatch(actions.setData(data, pulledFrom)),
        tryAuth: (props, forceAuth) =>
            dispatch(actions.tryAuth(props, forceAuth)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(FriendCard)
)
