import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import Checkbox from 'antd/es/checkbox'
import Cookies from 'universal-cookie'
import classes from './Auth.module.css'
import Modal from 'antd/es/modal'
import {
    FacebookLoginButton,
    GithubLoginButton,
    GoogleLoginButton,
} from 'react-social-login-buttons'

const cookies = new Cookies()

const Auth = props => {
    const [state, setState] = useState(false)
    const [cookieConsent, setCookieConsent] = useState(false)

    useEffect(() => {
        checkCookieConsent()
    })

    let remeberMeParam = '?rememberme=false'
    if (state) {
        remeberMeParam = '?rememberme=true'
    }

    let redirectParam = '&redirect=/login'
    if (props.lastUrl) {
        redirectParam = '&redirect=' + props.lastUrl
    }

    const checkCookieConsent = () => {
        const cookiesConsent = cookies.get('cookies_consent')
        if (cookiesConsent) {
            setCookieConsent(true)
        }
    }

    const agreeWithCookies = () => {
        cookies.set('cookies_consent', 'true', {
            path: '/',
            maxAge: 31530000,
        })
        checkCookieConsent()
    }
    const basicURL =
        process.env.NODE_ENV !== 'development'
            ? 'https://addspire.com/api/auth/'
            : 'http://my.websiter.test:5000/api/auth/'
    return !cookieConsent ? (
        <Modal
            title="Cookies consent"
            visible={!cookieConsent}
            onOk={agreeWithCookies}
            onCancel={() => {
                window.open('https://websiter.dev', '_self')
            }}
            destroyOnClose={true}
            maskClosable={false}
        >
            <p>We use cookies only for login purposes. </p>
            <p>
                In order to proceed you need to agree with the use of cookies.
            </p>
        </Modal>
    ) : (
        <div className="contentDiv">
            <div className={classes.Container}>
                <h1>Welcome to Addspire!</h1>
                <h2>This is where great progresses live.</h2>
                <h1>Continue with:</h1>
                <div className={classes.socialButtons}>
                    <FacebookLoginButton
                        onClick={() =>
                            (window.location =
                                basicURL + `facebook/start${remeberMeParam}`)
                        }
                    />
                    <GoogleLoginButton
                        onClick={() =>
                            (window.location =
                                basicURL + `google/start${remeberMeParam}`)
                        }
                    />
                    <GithubLoginButton
                        onClick={() =>
                            (window.location =
                                basicURL + `github/start${remeberMeParam}`)
                        }
                    />
                </div>
                <Checkbox checked={state} onChange={() => setState(!state)}>
                    Remember me
                </Checkbox>
            </div>
            <div className={classes.Background} />
        </div>
    )
}

const mapStateToProps = state => {
    return {
        lastUrl: state.lastUrl,
    }
}

export default withRouter(connect(mapStateToProps)(Auth))
