import React, { useEffect } from 'react'
import { connect } from 'react-redux'

import Button from 'antd/es/button'
import message from 'antd/es/message'

import Input from 'antd/es/input'
import PageHeader from 'antd/es/page-header'
import Form from 'antd/es/form'
import { withRouter } from 'react-router-dom'
import axios from 'axios'
import { nicknameRules, accountNameRules } from '../../constants/fieldRules'
import * as actions from '../../store/actions'

const ActivateAccount = props => {
    if (!props.isAuthed) props.history.push('/logout')
    useEffect(() => {
        if (!props.accountNotActive) props.history.push('/settings')
    }, [props.accountNotActive])
    const onFinish = value => {
        axios
            .post('/api/activate-account', {
                ...value,
            })
            .then(res => {
                if (res.data.success) {
                    message.success('Your account has been activated!')
                    props.setAccountActivated(false)
                } else {
                    if (res.data.nicknameNotUnique) {
                        message.warning(
                            'The nickname is taken. Choose another nickname please.'
                        )
                    }
                    if (res.data.errorCode) {
                        message.error(
                            'Something is wrong with the data. Please try one more time with new data.'
                        )
                    }
                }
            })
            .catch(err => {})
    }

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }
    const tailLayout = {
        wrapperCol: { offset: 8, span: 16 },
    }

    return (
        <>
            <PageHeader
                className="site-page-header"
                title="Activate your account"
                backIcon={false}
            />
            <Form
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                {...layout}
            >
                <Form.Item label="Name" name="name" rules={accountNameRules}>
                    <Input placeholder="Must be more than 1 character long" />
                </Form.Item>
                <Form.Item
                    label="Nickname"
                    name="nickname"
                    rules={nicknameRules}
                >
                    <Input placeholder="Must be more than 1 character long" />
                </Form.Item>
                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit" size="large">
                        Save
                    </Button>
                </Form.Item>
            </Form>
        </>
    )
}

const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        user: state.user,
        account: state.account,
        accountNotActive: state.accountNotActive,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        setAccountActivated: value =>
            dispatch(actions.setAccountActivated(value)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(ActivateAccount)
)
