import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'

import FriendCard from '../../components/FriendCard'
import AddCard from '../../components/AddCard/AddCard'
import AddFriends from './AddFriends'
import PendingFriends from './PendingFriends'
import Tabs from 'antd/es/tabs'
import Empty from 'antd/es/empty'
import Badge from 'antd/es/badge'
import MasonryGrid from '../../components/MasonryGrid'

const Friends = props => {
    const [addFriendOpen, setAddFriendOpen] = useState()

    useEffect(() => {
        props.setPageTitle({ main: 'Friends', sub: '' })
    }, [])

    const { account, friendData } = props
    if (!account._id) return null
    const realFriends = [],
        invitedFriends = [],
        invitingFriends = []

    if (account.friends) {
        for (let friend of account.friends) {
            const fullFriend = { ...friendData[friend.friend], ...friend }
            if (fullFriend) {
                if (fullFriend.status === 'friend') realFriends.push(fullFriend)
                else if (fullFriend.status === 'invited')
                    invitedFriends.push(fullFriend)
                else if (fullFriend.status === 'inviting')
                    invitingFriends.push(fullFriend)
            }
        }
    }

    const confirmedFriends =
        realFriends.length > 0 ? (
            <MasonryGrid friends={true}>
                {realFriends.map(friend => (
                    <FriendCard
                        {...props}
                        key={friend.friend}
                        friend={friend}
                        realFriends={realFriends}
                        invitingFriends={invitingFriends}
                        invitedFriends={invitedFriends}
                    />
                ))}
            </MasonryGrid>
        ) : (
            <Empty
                image="https://websiter.s3.us-east-2.amazonaws.com/image+4.jpg"
                imageStyle={{ height: '250px', marginTop: '50px' }}
                description={false}
            />
        )
    return (
        <div>
            <AddCard
                title="Add friend"
                sub="Find by nickname"
                action={() => setAddFriendOpen(true)}
            />
            {invitedFriends.length > 0 || invitingFriends.length > 0 ? (
                <Tabs>
                    <Tabs.TabPane key="confirmed" tab="Confrmed">
                        {confirmedFriends}
                    </Tabs.TabPane>

                    <Tabs.TabPane
                        tab={
                            <Badge
                                count={invitingFriends.length}
                                offset={[10, 0]}
                            >
                                Pending
                            </Badge>
                        }
                        key="pending"
                    >
                        <PendingFriends
                            realFriends={realFriends}
                            invitedFriends={invitedFriends}
                            invitingFriends={invitingFriends}
                        />
                    </Tabs.TabPane>
                </Tabs>
            ) : (
                confirmedFriends
            )}
            <AddFriends
                addFriendOpen={addFriendOpen}
                setAddFriendOpen={setAddFriendOpen}
                realFriends={realFriends}
                invitedFriends={invitedFriends}
                invitingFriends={invitingFriends}
                account={account}
            />
        </div>
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Friends)
)
