import React from 'react'
import * as wsActions from '../../websocketActions'

import EditGoalAndProgressForm from '../../components/EditGoalAndProgressForm/EditGoalAndProgressForm'

const AddProgress = props =>
    props.account._id ? (
        <EditGoalAndProgressForm
            onFinish={value => {
                wsActions.startProgress(props.account._id, value)
                props.setStartProgressVisible(false)
                document.body.removeAttribute('style')
            }}
            account={props.account}
            friendData={props.friendData}
            cancel={props.cancel}
        />
    ) : null

export default AddProgress
