import React from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import * as actions from '../../store/actions'
import Button from 'antd/es/button'
import Tag from 'antd/es/tag'
import TimeAgo from 'timeago-react'
import Avatar from 'antd/es/avatar'
import getAvatar from '../../utils/getAvatar'
import Rewards from '../../components/Rewards/Rewards'
import Collapse from 'antd/es/collapse'
import Badge from 'antd/es/badge'
import UserButton from '../../components/UserButton/UserButton'
import Descriptions from 'antd/es/descriptions'
import Typography from 'antd/es/typography'

const Transactions = props => {
    props.setPageTitle({ main: 'Shop', sub: 'Transactions' })

    const { account, transactionData, friendData } = props
    if (!account || !account._id) return null

    const transactions = account.transactions
        .filter(item => transactionData[item])
        .map(item => transactionData[item])

    const columns = [
        {
            title: 'Type',
            dataIndex: 'types',
            key: 'types',
            render: types => (
                <>
                    {types.map(tag => {
                        let color = tag.length > 5 ? 'geekblue' : 'green'
                        if (tag === 'loser') {
                            color = 'volcano'
                        }
                        return (
                            <Tag color={color} key={tag}>
                                {tag.toUpperCase()}
                            </Tag>
                        )
                    })}
                </>
            ),
        },
        {
            title: 'With',
            dataIndex: 'user',
            key: 'user',
            render: user => {
                const friend = friendData[user]
                return friend ? (
                    <Button
                        type="link"
                        style={{ padding: 0 }}
                        onClick={() => props.history.push('/profile/' + user)}
                    >
                        <Avatar
                            src={getAvatar(user, friend.image)}
                            style={{ marginRight: '5px' }}
                        />
                        {friend.name + ' (' + friend._id + ')'}
                    </Button>
                ) : (
                    user
                )
            },
        },
        {
            title: 'Item',
            dataIndex: 'perk',
            key: 'perk',
            render: perk =>
                perk.simple || (
                    <Rewards
                        rewardKey={1}
                        label="Rewards"
                        rewards={[{ rewards: [perk], key: 1 }]}
                        readOnly={true}
                    />
                ),
        },
        {
            title: 'Amount',
            dataIndex: 'amount',
            key: 'amount',
            render: amount => (amount > 0 ? amount : ''),
        },
        {
            title: 'Date',
            dataIndex: 'date',
            key: 'date',
            render: date => <TimeAgo datetime={date} live />,
        },
        {
            title: 'Progress',
            dataIndex: 'progress',
            key: 'progress',
            render: progress => (
                <Button
                    type="link"
                    onClick={() =>
                        props.history.push('/inspirations/' + progress)
                    }
                >
                    {progress}
                </Button>
            ),
        },
        {
            title: 'Status',
            key: 'status',
            dataIndex: 'status',
            render: (status, record) => {
                if (status === 'Not confirmed') {
                    return (
                        <>
                            <Button
                                danger={true}
                                type="link"
                                onClick={() =>
                                    wsActions.cancelTransaction(
                                        account._id,
                                        record.transaction._id
                                    )
                                }
                            >
                                Cancel
                            </Button>
                            {record.transaction.from === account._id && (
                                <Button
                                    type="link"
                                    onClick={() =>
                                        wsActions.confirmTransaction(
                                            account._id,
                                            record.transaction._id
                                        )
                                    }
                                >
                                    Confirm
                                </Button>
                            )}
                        </>
                    )
                }
                return status
            },
        },
    ]

    const tableData = []
    let sorted = [...transactions]
    sorted = sorted.sort((a, b) =>
        new Date(a.date).getTime() < new Date(b.date).getTime() ? 1 : -1
    )
    for (let transaction of sorted) {
        const types = []
        if (transaction.from === account._id && transaction.to === account.id) {
            types.push('internal')
        }
        if (transaction.to === account._id) {
            if (transaction.progress) {
                types.push('earn')
                if (!transaction.amount) types.push('item')
                else types.push('money')
            } else {
                if (transaction.item && transaction.amount) {
                    types.push('buy')
                    types.push('item')
                } else {
                    types.push('recieve gift')
                    if (!transaction.amount) types.push('item')
                    else types.push('money')
                }
            }
        } else if (transaction.from === account._id) {
            if (transaction.progress) {
                types.push('pay')
                if (!transaction.amount) types.push('item')
                else types.push('money')
            } else {
                if (transaction.item && transaction.amount) {
                    types.push('sell')
                    types.push('item')
                } else {
                    types.push('send gift')
                    if (!transaction.amount) types.push('item')
                    else types.push('money')
                }
            }
        }

        const user =
            transaction.to !== account._id ? transaction.to : transaction.from

        tableData.push({
            key: tableData.length,
            types,
            user,
            perk: transaction.item,
            amount: transaction.amount,
            date: transaction.date,
            progress: transaction.progress,
            status: transaction.status,
            transaction,
        })
    }
    return (
        <Collapse
            expandIconPosition="right"
            style={{ marginBottom: '50px' }}
            destroyInactivePanel={true}
            accordion
        >
            {sorted.map((transaction, index) => {
                const types = []
                if (
                    transaction.from === account._id &&
                    transaction.to === account.id
                ) {
                    types.push('internal')
                }
                if (transaction.to === account._id) {
                    if (transaction.progress) {
                        types.push('earn')
                        if (!transaction.amount) types.push('item')
                        else types.push('money')
                    } else {
                        if (transaction.item && transaction.amount) {
                            types.push('buy')
                            types.push('item')
                        } else {
                            types.push('recieve gift')
                            if (!transaction.amount) types.push('item')
                            else types.push('money')
                        }
                    }
                } else if (transaction.from === account._id) {
                    if (transaction.progress) {
                        types.push('pay')
                        if (!transaction.amount) types.push('item')
                        else types.push('money')
                    } else {
                        if (transaction.item && transaction.amount) {
                            types.push('sell')
                            types.push('item')
                        } else {
                            types.push('send gift')
                            if (!transaction.amount) types.push('item')
                            else types.push('money')
                        }
                    }
                }

                const user =
                    transaction.to !== account._id
                        ? transaction.to
                        : transaction.from

                tableData.push({
                    key: tableData.length,
                    types,
                    user,
                    perk: transaction.item,
                    amount: transaction.amount,
                    date: transaction.date,
                    progress: transaction.progress,
                    status: transaction.status,
                    transaction,
                })
                const { item: reward } = transaction
                return (
                    <Collapse.Panel
                        style={{ padding: 0 }}
                        header={
                            <>
                                <div
                                    style={{
                                        display: 'flex',
                                        alignItems: 'center',
                                    }}
                                >
                                    <Badge
                                        color={
                                            transaction.status ===
                                            'Not confirmed'
                                                ? 'blue'
                                                : transaction.status ===
                                                  'Cancelled'
                                                ? 'red'
                                                : 'green'
                                        }
                                    />
                                    <Typography.Paragraph
                                        ellipsis={{ rows: 1 }}
                                        style={{ display: 'inline', margin: 0 }}
                                    >
                                        {reward.mode === 'simple'
                                            ? reward.simple
                                            : reward.mode === 'money'
                                            ? reward.money + ' (money)'
                                            : reward.mode === 'item'
                                            ? reward.itemName
                                            : ''}
                                    </Typography.Paragraph>
                                    <UserButton
                                        noName={true}
                                        id={
                                            transaction.to !== account._id
                                                ? transaction.to
                                                : transaction.from
                                        }
                                    />
                                </div>
                                <div>
                                    <TimeAgo datetime={transaction.date} live />
                                </div>
                            </>
                        }
                        key={index}

                        // extra={}
                    >
                        <Descriptions
                            layout="vertical"
                            size="small"
                            bordered
                            column={{
                                xxl: 4,
                                xl: 3,
                                lg: 3,
                                md: 3,
                                sm: 2,
                                xs: 1,
                            }}
                        >
                            <Descriptions.Item label="Type">
                                {types.map(tag => {
                                    let color =
                                        tag.length > 5 ? 'geekblue' : 'green'
                                    if (tag === 'loser') {
                                        color = 'volcano'
                                    }
                                    return (
                                        <Tag color={color} key={tag}>
                                            {tag.toUpperCase()}
                                        </Tag>
                                    )
                                })}
                            </Descriptions.Item>
                            <Descriptions.Item label="Transaction with">
                                <UserButton
                                    id={
                                        transaction.to !== account._id
                                            ? transaction.to
                                            : transaction.from
                                    }
                                />
                            </Descriptions.Item>

                            <Descriptions.Item label="Item">
                                {
                                    <Rewards
                                        onlyButton={true}
                                        rewardKey={1}
                                        label="Rewards"
                                        rewards={[
                                            {
                                                rewards: [transaction.item],
                                                key: 1,
                                            },
                                        ]}
                                        readOnly={true}
                                    />
                                }
                            </Descriptions.Item>

                            {transaction.amount && (
                                <Descriptions.Item label="Amount">
                                    {transaction.amount}
                                </Descriptions.Item>
                            )}
                            <Descriptions.Item label="Date">
                                {date.format(
                                    new Date(transaction.date),
                                    'YYYY/MM/DD HH:mm:ss'
                                )}
                            </Descriptions.Item>

                            {transaction.progress && transaction.progressId && (
                                <Descriptions.Item label="Progress">
                                    <Button
                                        type="text"
                                        onClick={() =>
                                            props.history.push(
                                                '/inspirations/' +
                                                    transaction.progressId
                                            )
                                        }
                                    >
                                        {transaction.progress}
                                    </Button>
                                </Descriptions.Item>
                            )}
                            <Descriptions.Item label="Status">
                                {status === 'Not confirmed' ? (
                                    <>
                                        <Button
                                            danger={true}
                                            type="link"
                                            onClick={() =>
                                                wsActions.cancelTransaction(
                                                    account._id,
                                                    record.transaction._id
                                                )
                                            }
                                        >
                                            Cancel
                                        </Button>
                                        {record.transaction.from ===
                                            account._id && (
                                            <Button
                                                type="link"
                                                onClick={() =>
                                                    wsActions.confirmTransaction(
                                                        account._id,
                                                        record.transaction._id
                                                    )
                                                }
                                            >
                                                Confirm
                                            </Button>
                                        )}
                                    </>
                                ) : (
                                    status || 'Confirmed'
                                )}
                            </Descriptions.Item>
                        </Descriptions>
                    </Collapse.Panel>
                )
            })}
        </Collapse>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        transactionData: state.transactionData,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Transactions)
)
