import { setData } from './auth'

export const saveResource = (data, type) => (dispatch, getState) => {
    if (data.length > 0) {
        const state = getState()
        switch (type) {
            case 'user':
                const user = data[0]
                let currentAccount = user.currentAccount
                if (!currentAccount) currentAccount = user.myAccount
                // if(!currentAccount)
                //activate-account
                dispatch(setData({ user, currentAccount }))
                break
            case 'account':
                const account = data.find(
                    item => item._id === state.currentAccount
                )
                if (account) {
                    dispatch(setData({ account }))
                }

                const profile = data.find(
                    item => item._id === state.currentProfile
                )
                if (profile) {
                    dispatch(setData({ profile }))
                }

                break
            case 'progress':
                const progress = data.find(
                    item => item._id === state.currentProgress
                )
                if (progress) {
                    dispatch(setData({ progress }))
                }
                break

            case 'post':
                let post = {}
                for (let item of data) {
                    post[item._id] = item
                }
                dispatch(setData({ post }))
                break
            case 'friendData':
                let friendData = {}
                for (let item of data) {
                    friendData[item._id] = item
                }
                dispatch(setData({ friendData }))
                break
            case 'progressData':
                let progressData = {}
                for (let item of data) {
                    progressData[item._id] = item
                }
                dispatch(setData({ progressData }))
                break
            case 'transactionData':
                let transactionData = {}
                for (let item of data) {
                    transactionData[item._id] = item
                }
                dispatch(setData({ transactionData }))
                break
            case 'postData':
                let postData = {}
                for (let item of data) {
                    postData[item._id] = item
                }
                dispatch(setData({ postData }))
                break

            default:
                break
        }
    }
}
