import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Form from 'antd/es/form'
import Input from 'antd/es/input'

import { withRouter } from 'react-router-dom'
import Card from 'antd/es/card'
import Upload from 'antd/es/upload'
import * as classes from './Settings.module.css'

import LoadingOutlined from '@ant-design/icons/LoadingOutlined'
import PlusOutlined from '@ant-design/icons/PlusOutlined'

import { uploadFile } from '../../utils/uploadFile'
import Avatar from 'antd/es/avatar'
import getAvatar from '../../utils/getAvatar'
import Modal from 'antd/es/modal'
import { accountNameRules } from '../../constants/fieldRules'
import * as wsActions from '../../websocketActions'
import message from 'antd/es/message'
import Dropdown from 'antd/es/dropdown'
import Menu from 'antd/es/menu'
import Helmet from 'react-helmet'

const Settings = props => {
    const [deleteVisible, setDeleteVisible] = useState()
    const [deleteField, setDeleteField] = useState('')
    const { account } = props

    useEffect(() => {
        props.setPageTitle({ main: 'Settings', sub: '' })
    }, [])

    const loading = false

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }
    const tailLayout = {
        wrapperCol: { offset: 8, span: 0 },
    }
    return account && account.name ? (
        <div className="contentDiv">
            <Helmet>
                <title>Account settings Addspire</title>
                <meta
                    name="description"
                    content="Addspire is a social network where you can inspire and motivate yourself or the people you care."
                />
            </Helmet>
            <Card>
                <Upload
                    name="avatar"
                    listType="picture-card"
                    className={classes.Avatar}
                    showUploadList={false}
                    customRequest={({ file }) => {
                        uploadFile(file, 800, 'avatar', () => {
                            props.increaseAvatarV()
                        })
                    }}
                >
                    {account.image ? (
                        <Avatar
                            src={getAvatar(account._id, account.image)}
                            size={150}
                            style={{ margin: '25px' }}
                        />
                    ) : (
                        <div>
                            {loading ? <LoadingOutlined /> : <PlusOutlined />}
                            <div className="ant-upload-text">Upload</div>
                        </div>
                    )}
                </Upload>
                <Form
                    name="dynamic_form_nest_item"
                    onFinish={value =>
                        wsActions.editAccount(value, account._id)
                    }
                    autoComplete="off"
                    {...layout}
                    initialValues={{
                        name: account.name,
                        nickname: account._id,
                    }}
                >
                    <Form.Item
                        label="Name"
                        name="name"
                        rules={accountNameRules}
                    >
                        <Input placeholder="Must be more than 1 character long" />
                    </Form.Item>
                    <Form.Item label="Nickname" name="nickname">
                        <Input disabled={true} />
                    </Form.Item>
                    <Form.Item
                        {...tailLayout}
                        style={{
                            flex: 'none',
                            maxWidth: ' 100%',
                            overflow: 'hidden',
                        }}
                    >
                        <Dropdown.Button
                            type="primary"
                            htmlType="submit"
                            size="large"
                            overlay={
                                <Menu>
                                    <Menu.Item
                                        danger={true}
                                        onClick={() => setDeleteVisible(true)}
                                    >
                                        Delete account
                                    </Menu.Item>
                                </Menu>
                            }
                        >
                            Save
                        </Dropdown.Button>
                    </Form.Item>
                </Form>
                <Modal
                    title="Delete account"
                    visible={deleteVisible}
                    onOk={() => {
                        if (deleteField !== account._id) {
                            message.warning('The nickmanme is not correct.')
                        } else {
                            wsActions.deleteAccount(account._id)
                        }
                    }}
                    onCancel={() => {
                        setDeleteVisible(false)
                        setDeleteField('')
                    }}
                >
                    <p>
                        Please enter your nickname and press Ok to delete your
                        account.
                    </p>
                    <Input onChange={e => setDeleteField(e.target.value)} />
                </Modal>
            </Card>
        </div>
    ) : null
}

const mapStateToProps = state => {
    return {
        account: state.account,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        increaseAvatarV: () => dispatch(actions.increaseAvatarV()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Settings)
)
