import React, { useState } from 'react'
import Modal from 'antd/es/modal'
import Button from 'antd/es/button'
import AddPerk from './AddPerk'
import PerkCard from './PerkCard'
import AddCard from '../../components/AddCard/AddCard'
import MasonryGrid from '../../components/MasonryGrid'

const WishlistAndPerks = props => {
    const [editedPerk, setEditedPerk] = useState('')
    const [editPerkOpen, setEditPerkOpen] = useState()

    const { account } = props

    const editPerk = (
        <Modal
            title="Add item"
            visible={editPerkOpen}
            maskClosable
            closable
            onCancel={() => setEditPerkOpen(false)}
            width="90%"
            footer={
                <Button key="back" onClick={() => setEditPerkOpen(false)}>
                    Close
                </Button>
            }
            destroyOnClose={true}
        >
            <AddPerk
                setEditPerkOpen={setEditPerkOpen}
                perkId={editedPerk}
                mode={props.mode}
            />
        </Modal>
    )

    return account._id ? (
        <>
            <AddCard
                action={() => {
                    setEditedPerk('')
                    setEditPerkOpen(true)
                }}
                title="Add item"
                sub={props.mode === 'shop' && 'Set price'}
            />
            <MasonryGrid>
                {account[props.listKey || props.mode] &&
                    account[props.listKey || props.mode].map((perk, index) => (
                        <PerkCard
                            key={index}
                            perk={perk}
                            perkId={
                                props.mode === 'wishlist' ||
                                props.mode === 'goals'
                                    ? perk.itemId
                                    : perk.perkId
                            }
                            owner={account}
                            account={account}
                            setEditedPerk={setEditedPerk}
                            setEditPerkOpen={setEditPerkOpen}
                            mode={props.mode}
                            style={{ cursor: 'pointer' }}
                        />
                    ))}
            </MasonryGrid>

            {editPerk}
        </>
    ) : null
}

export default WishlistAndPerks
