import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import Checkbox from 'antd/es/checkbox'
import Cookies from 'universal-cookie'
import classes from '../../containers/Auth/Auth.module.css'
import Modal from 'antd/es/modal'
import {
    FacebookLoginButton,
    GithubLoginButton,
    GoogleLoginButton,
} from 'react-social-login-buttons'
import Typography from 'antd/es/typography'

const cookies = new Cookies()

const LoginModal = props => {
    const [state, setState] = useState(false)
    const [cookieConsent, setCookieConsent] = useState(false)

    useEffect(() => {
        checkCookieConsent()
    })

    let remeberMeParam = '?rememberme=false'
    if (state) {
        remeberMeParam = '?rememberme=true'
    }

    let redirectParam = '&redirect=/login'
    if (props.lastUrl) {
        redirectParam = '&redirect=' + props.lastUrl
    }

    const checkCookieConsent = () => {
        const cookiesConsent = cookies.get('cookies_consent')
        if (cookiesConsent) {
            setCookieConsent(true)
        }
    }

    const agreeWithCookies = () => {
        cookies.set('cookies_consent', 'true', {
            path: '/',
            maxAge: 31530000,
        })
        checkCookieConsent()
    }
    const basicURL =
        process.env.NODE_ENV !== 'development'
            ? 'https://addspire.com/api/auth/'
            : 'http://my.websiter.test:5000/api/auth/'
    return (
        <Modal
            visible={props.loginVisible}
            onOk={() => {}}
            onCancel={() => props.setLoginVisible(false)}
            destroyOnClose={true}
            maskClosable={false}
            footer={[]}
            width="100%"
            style={{ maxWidth: '600px' }}
        >
            {!cookieConsent ? (
                <Modal
                    title="Cookies consent"
                    visible={!cookieConsent}
                    onOk={agreeWithCookies}
                    onCancel={() => {
                        window.open('https://addspire.com', '_self')
                    }}
                    destroyOnClose={true}
                    maskClosable={false}
                    width="100%"
                    style={{ maxWidth: '600px' }}
                >
                    <p style={{ marginBottom: '0.3em' }}>
                        We use cookies only for login purposes.{' '}
                    </p>
                    <p>
                        In order to proceed you need to agree with the use of
                        cookies.
                    </p>
                </Modal>
            ) : (
                <div className="contentDiv">
                    <div className={classes.Container}>
                        <h1
                            style={{
                                fontWeight: 'bold',
                                lineHeight: '1.3em',
                                marginBottom: 0,
                                fontSize: '2.5em',
                            }}
                        >
                            Welcome to Addspire!
                        </h1>
                        <h3>This is where great inspiration lives.</h3>
                        <div
                            className={classes.socialButtons}
                            style={{ marginBottom: '30px' }}
                        >
                            <FacebookLoginButton
                                onClick={() =>
                                    (window.location =
                                        basicURL +
                                        `facebook/start${remeberMeParam}`)
                                }
                            />
                            <GoogleLoginButton
                                s
                                onClick={() =>
                                    (window.location =
                                        basicURL +
                                        `google/start${remeberMeParam}`)
                                }
                            />
                            <GithubLoginButton
                                onClick={() =>
                                    (window.location =
                                        basicURL +
                                        `github/start${remeberMeParam}`)
                                }
                            />
                        </div>
                        <Checkbox
                            checked={state}
                            onChange={() => setState(!state)}
                        >
                            <Typography.Link style={{ fontSize: '17px' }}>
                                Remember me
                            </Typography.Link>
                        </Checkbox>
                    </div>
                    <div className={classes.Background} />
                </div>
            )}
        </Modal>
    )
}

const mapStateToProps = state => {
    return {
        lastUrl: state.lastUrl,
    }
}

export default withRouter(connect(mapStateToProps)(LoginModal))
