import React from 'react'
import { Link, withRouter } from 'react-router-dom'

export default withRouter(props => {
    const Card = (
        <>
            {props.images && props.images.length > 0 && (
                <img
                    style={{
                        maxWidth: '100%',
                        margin: 'auto',
                        display: 'block',
                        borderRadius: '15px',
                    }}
                    src={props.images.length > 0 ? props.images[0] : ''}
                />
            )}
            <div style={{ padding: '5px 0px', wordBreak: 'break-word' }}>
                {props.actions && (
                    <div style={{ float: 'right' }}>{props.actions}</div>
                )}
                <span style={{ float: 'none' }}>{props.body}</span>
            </div>
        </>
    )
    return props.noLink ? (
        Card
    ) : (
        <Link to={props.onClickLink} style={{ cursor: 'pointer' }}>
            {Card}
        </Link>
    )
})
