import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'

import Comment from 'antd/es/comment'

import Tooltip from 'antd/es/tooltip'
import TimeAgo from 'timeago-react'

import * as wsActions from '../../websocketActions'
import LikeFilled from '@ant-design/icons/LikeFilled'
import LikeOutlined from '@ant-design/icons/LikeOutlined'
import AddMessage from './AddMessage'
import Button from 'antd/es/button'
import ImageCarousel from '../UI/ImageCarousel/ImageCarousel'
import UserButton from '../UserButton/UserButton'

const Message = props => {
    const {
        account,
        message,
        friendData,
        progressData,
        post,
        replies,
        isStart,
    } = props
    const [replyVisible, setReplyVisible] = useState(false)
    const [replyToUser, setReplyToUser] = useState('')
    const [editingMessage, setEditingMessage] = useState('')
    const [showReplies, setShowReplies] = useState(false)
    const commentProps = {
        actions: !props.isStart && [
            <span>
                <TimeAgo datetime={message.date} live />
            </span>,
            <span key="comment-basic-like">
                <Tooltip title="Like">
                    {message.likes.includes(account._id) ? (
                        <LikeFilled
                            onClick={() =>
                                wsActions.changeLikesMessage(
                                    'dislikeMessage',
                                    message.messageId,
                                    account._id,
                                    post._id
                                )
                            }
                        />
                    ) : (
                        <LikeOutlined
                            onClick={() =>
                                wsActions.changeLikesMessage(
                                    'likeMessage',
                                    message.messageId,
                                    account._id,
                                    post._id
                                )
                            }
                        />
                    )}
                </Tooltip>
                <span style={{ paddingLeft: '8px' }}>
                    {message.likes.length}
                </span>
            </span>,

            message.author !== account._id && (
                <span
                    key="comment-basic-reply-to"
                    onClick={() => {
                        if (props.setReplyVisible) {
                            props.setReplyVisible(true)
                            props.setReplyToUser(props.message.author)
                        } else {
                            setReplyVisible(true)
                            setReplyToUser('')
                        }
                        setShowReplies(true)
                    }}
                >
                    Reply
                </span>
            ),
            message.author === account._id && (
                <span
                    key="comment-basic-reply-to"
                    onClick={() => setEditingMessage(message.messageId)}
                >
                    Edit
                </span>
            ),
        ],
        avatar: <UserButton noName={true} id={message.author} span={true} />,
        content: isStart ? (
            <>
                {message.messageType === 'wishlist' && (
                    <>
                        {' '}
                        <span
                            style={{
                                fontWeight: 'bold',
                                cursor: 'pointer',
                                margin: '10px 0px 0px ',
                            }}
                            onClick={() =>
                                props.history.push(
                                    `/wishlist/${message.details.owner}/${
                                        message.details.itemId
                                    }`
                                )
                            }
                        >
                            {message.details.name}
                        </span>
                    </>
                )}
                {message.text && <p>{message.text}</p>}
            </>
        ) : (
            <>
                <div
                    style={{
                        background: '#eee',
                        borderRadius: '5px',
                        padding: '3px 10px',
                        display:
                            message.image && message.image.length > 0
                                ? 'block'
                                : 'inline-block',
                    }}
                >
                    <UserButton
                        id={message.author}
                        span={true}
                        noAva={true}
                        style={{ fontWeight: 'bold' }}
                    />

                    {message.image && message.image.length > 0 && (
                        <ImageCarousel
                            images={message.image}
                            height="60vmin"
                            type="contain"
                        />
                    )}

                    {message.text && <p>{message.text}</p>}
                </div>
                <div />
            </>
        ),
        author: isStart && (
            <span
                style={{ color: '#333', fontWeight: 'bold', fontSize: '14px' }}
            >
                <UserButton id={message.author} span={true} noAva={true} />{' '}
                <span style={{ fontWeight: 'normal' }}>in</span>{' '}
                {post.parent &&
                    progressData &&
                    progressData[post.parent.parentId] && (
                        <span
                            style={{ cursor: 'pointer' }}
                            onClick={() =>
                                props.history.push(
                                    '/inspirations/' + post.parent.parentId
                                )
                            }
                        >
                            {progressData[post.parent.parentId].goal.name}
                        </span>
                    )}
                {post.startMessage &&
                    ['wishlist', 'perk', 'goal'].includes(
                        post.startMessage.messageType
                    ) && (
                        <span
                            style={{ cursor: 'pointer' }}
                            onClick={() =>
                                props.history.push('/profile/' + message.author)
                            }
                        >
                            {post.startMessage.messageType === 'wishlist'
                                ? 'Wishlist'
                                : post.startMessage.messageType === 'goals'
                                ? 'Goals'
                                : 'Shop'}
                        </span>
                    )}
            </span>
        ),
        datetime: isStart && (
            <span style={{ color: '#333' }}>
                <TimeAgo datetime={message.date} live />
            </span>
        ),
    }
    return account && friendData ? (
        account._id !== message.author || !editingMessage ? (
            <Comment {...commentProps}>
                {props.isTop && replies && replies.length > 0 ? (
                    showReplies ? (
                        replies.map((reply, index) => (
                            <Message
                                key={index}
                                message={reply}
                                post={post}
                                account={account}
                                friendData={friendData}
                                setReplyVisible={setReplyVisible}
                                setReplyToUser={setReplyToUser}
                            />
                        ))
                    ) : (
                        <Button
                            type="text"
                            onClick={() => setShowReplies(true)}
                        >
                            Show {replies.length} replies
                        </Button>
                    )
                ) : null}

                {props.isTop && replyVisible ? (
                    <AddMessage
                        post={post}
                        account={account}
                        replyTo={message.messageId}
                        replyToUser={replyToUser}
                        cancel={() => setReplyVisible(false)}
                    />
                ) : null}
            </Comment>
        ) : (
            <AddMessage
                post={post}
                account={account}
                replyTo={message.messageId}
                editingMessage={editingMessage}
                content={message.text}
                images={message.image}
                onSend={() => setEditingMessage('')}
                cancel={() => setEditingMessage('')}
            />
        )
    ) : null
}

export default withRouter(Message)
