import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'
import Button from 'antd/es/button'

import Modal from 'antd/es/modal'

import Descriptions from 'antd/es/descriptions'

const { Item } = Descriptions

import * as wsActions from '../../websocketActions'
import ActivityTable from './ActivityTable'
import EditProgress from './EditProgress'

import Typography from 'antd/es/typography'
import UserButton from '../../components/UserButton/UserButton'
import ImageCarousel from '../../components/UI/ImageCarousel/ImageCarousel'

import Tag from 'antd/es/tag'
import Rewards from '../../components/Rewards/Rewards'
import MasonryCard from '../../components/MasonryCard'
import FollowButton from '../../components/FollowButton'
import Helmet from 'react-helmet'
import LikeFilled from '@ant-design/icons/LikeFilled'
import LikeOutlined from '@ant-design/icons/LikeOutlined'

const Activity = props => {
    const { account, progress, state, setState, friendData } = props
    const { goal } = progress
    const [followersVisible, setFollowersVisible] = useState(false)

    const getDay = day => {
        switch (day) {
            case '0':
                return 'Sunday'
            case '1':
                return 'Monday'
            case '2':
                return 'Tuesday'
            case '3':
                return 'Wednesday'
            case '4':
                return 'Thursday'
            case '5':
                return 'Friday'
            case '6':
                return 'Saturday'

            default:
                return ''
        }
    }
    return goal ? (
        <>
            <Helmet>
                <title>
                    {goal.name} by {progress.owner} at Addspire
                </title>
                <meta name="description" content={goal.description} />
            </Helmet>
            {goal.images.length > 0 && <ImageCarousel images={goal.images} />}

            <h1>{goal.name}</h1>
            <Button type="text" onClick={() => setFollowersVisible(true)}>
                {progress.followingAccounts.length} Followers
            </Button>
            <Button
                type="text"
                onClick={() =>
                    wsActions.changeLikesProgress(
                        account._id,
                        progress._id,
                        !progress.likes.includes(account._id)
                    )
                }
            >
                {progress.likes.includes(account._id) ? (
                    <LikeFilled style={{ fontSize: '20px' }} />
                ) : (
                    <LikeOutlined style={{ fontSize: '20px' }} />
                )}
                Likes
                <span style={{ paddingLeft: '8px' }}>
                    {progress.likes.length}
                </span>
            </Button>

            <div>
                {account &&
                account._id &&
                account.followProgresses.includes(progress._id) ? (
                    <Button
                        type="link"
                        onClick={() =>
                            wsActions.unfollowProgress(
                                account._id,
                                progress._id
                            )
                        }
                    >
                        Following
                    </Button>
                ) : (
                    <Button
                        type="primary"
                        onClick={() =>
                            wsActions.followProgress(account._id, progress._id)
                        }
                    >
                        Follow
                    </Button>
                )}
            </div>
            {account._id === progress.owner && (
                <Button
                    style={{ margin: '0px 20px 50px 0px' }}
                    type="primary"
                    onClick={() => {
                        setState({ ...state, editProgressOpen: true })
                    }}
                >
                    Edit progress
                </Button>
            )}
            <Typography.Paragraph
                ellipsis={{
                    rows: 3,
                    expandable: true,
                    symbol: 'more',
                }}
                style={{ whiteSpace: 'pre-wrap' }}
            >
                {goal.description}
            </Typography.Paragraph>

            <Descriptions
                size="small"
                bordered
                column={{ xxl: 4, xl: 3, lg: 3, md: 3, sm: 2, xs: 1 }}
            >
                <Item label="Repeat">{goal.repeat || ''}</Item>
                {goal.repeat === 'weekday' && (
                    <Item label="Days">
                        {goal.days.map((day, index) => (
                            <Tag key={index}>{getDay(day)}</Tag>
                        ))}
                    </Item>
                )}
                <Item label="Rewards">
                    <Rewards rewards={goal.rewards} />
                </Item>
            </Descriptions>
            <div style={{ margin: '50px 0px' }}>
                <ActivityTable
                    progress={progress}
                    account={account}
                    state={state}
                    setState={setState}
                />
            </div>

            {account.progresses &&
                account.progresses.includes(progress._id.toString()) && (
                    <Button
                        danger={true}
                        onClick={() => {
                            wsActions.leaveProgress(props)
                        }}
                    >
                        Leave the progress
                    </Button>
                )}

            <Modal
                visible={state.editProgressOpen}
                footer={null}
                onCancel={() => setState({ ...state, editProgressOpen: false })}
                closable={true}
                destroyOnClose={true}
                width="100%"
                style={{ maxWidth: '1000px' }}
            >
                <EditProgress
                    progress={progress}
                    account={account}
                    state={state}
                    setState={setState}
                    friendData={friendData}
                    cancel={() =>
                        setState({ ...state, editProgressOpen: false })
                    }
                />
            </Modal>
            <Modal
                title="Followers"
                visible={followersVisible}
                footer={null}
                onCancel={() => setFollowersVisible(false)}
            >
                {progress.followingAccounts.map(item => (
                    <MasonryCard
                        noLink={true}
                        key={item}
                        actions={<FollowButton account={account} id={item} />}
                        body={<UserButton span={true} id={item} size={50} />}
                    />
                ))}
            </Modal>
        </>
    ) : null
}

export default withRouter(Activity)
