const Blog = props => {
    console.log(props.history.location.pathname)
    const [state, setState] = useState('')
    const [title, setTitle] = useState('')
    useEffect(() => {
        const url = 'https://websiter.dev' + props.history.location.pathname
        console.log(url)
        axios
            .post('/api/blog', { url })
            .then(res => {
                setState(
                    styles +
                        res.data.body
                            .replace(
                                /\/images\/blog/gi,
                                'https://websiter.dev/images/blog'
                            )
                            .replace(
                                /href="https:\/\/websiter.dev/gi,
                                'href="https://addspire.com'
                            )
                )
                setTitle(res.data.title)
            })
            .catch(err => {})
    }, [props.history.location.pathname])

    return (
        <div>
            {title && (
                <Helmet>
                    <title>{title}</title>
                </Helmet>
            )}
            <div dangerouslySetInnerHTML={{ __html: state }} />
        </div>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Blog)
)
