import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import * as wsActions from '../../websocketActions'
import Card from 'antd/es/card'
import Button from 'antd/es/button'
import Modal from 'antd/es/modal'

import Input from 'antd/es/input'
import Form from 'antd/es/form'

import FriendCard from '../../components/FriendCard'
import { nicknameRules } from '../../constants/fieldRules'

const AddFriends = props => {
    useEffect(() => {
        props.setFriendSearchResult({})
    }, [props.addFriendOpen])

    const {
        addFriendOpen,
        setAddFriendOpen,
        searchResult,
        realFriends,
        invitedFriends,
        invitingFriends,
    } = props
    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }
    const tailLayout = {
        wrapperCol: { offset: 8, span: 16 },
    }

    return (
        <Modal
            title="Add friend"
            visible={addFriendOpen}
            maskClosable
            closable
            onCancel={() => setAddFriendOpen(false)}
            footer={
                <Button key="back" onClick={() => setAddFriendOpen(false)}>
                    Return
                </Button>
            }
            destroyOnClose={true}
        >
            <p>
                To find a friend you need to enter your friend's name or
                nickname.
            </p>
            <Form
                name="dynamic_form_nest_item"
                onFinish={value => wsActions.searchFriends(value.search)}
                autoComplete="off"
                {...layout}
            >
                <Form.Item label="Search" name="search" rules={nicknameRules}>
                    <Input placeholder="Must be more than 2 characters long" />
                </Form.Item>

                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit" size="large">
                        Search
                    </Button>
                </Form.Item>
            </Form>
            {searchResult._id && (
                <Card title="Search result">
                    <FriendCard
                        {...props}
                        friend={searchResult}
                        realFriends={realFriends}
                        invitingFriends={invitingFriends}
                        invitedFriends={invitedFriends}
                    />
                </Card>
            )}
        </Modal>
    )
}

const mapStateToProps = state => {
    return {
        searchResult: state.friendSearchResult,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setFriendSearchResult: data =>
            dispatch(actions.setFriendSearchResult(data)),
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(AddFriends)
