import React from 'react'
import Button from 'antd/es/button'
import * as wsActions from '../websocketActions'

const FollowButton = props => {
    const { account, type, id } = props
    if (account && account._id && account._id !== id) {
        if (type === 'progress') {
            return account.followProgresses.includes(id) ? (
                <Button
                    danger
                    onClick={() => wsActions.unfollowProgress(account._id, id)}
                >
                    Unfollow
                </Button>
            ) : (
                <Button
                    type="primary"
                    onClick={() => wsActions.followProgress(account._id, id)}
                >
                    Follow
                </Button>
            )
        } else {
            return account.followAccounts.includes(id) ? (
                <Button
                    danger
                    onClick={() => wsActions.unfollowAccount(account._id, id)}
                >
                    Unfollow
                </Button>
            ) : (
                <Button
                    type="primary"
                    onClick={() => wsActions.followAccount(account._id, id)}
                >
                    Follow
                </Button>
            )
        }
    }
    return null
}

export default FollowButton
