import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import Avatar from 'antd/es/avatar'

const ProgressButton = props => {
    const { id } = props
    const userName = id => props.progressData[id].goal.name
    const userAvatar = id => (
        <span
            onClick={() => {
                handleClick(id)
            }}
        >
            <Avatar
                src={
                    props.progressData[id].goal.images.length > 0 &&
                    props.progressData[id].goal.images[0]
                }
                size={props.size || (props.noClick ? 20 : 40)}
                style={props.group ? {} : { margin: ' 0 5px' }}
            />
        </span>
    )

    const handleClick = id =>
        props.history.push('/inspirations/' + (id || props.id))
    return (
        <span
            style={{ cursor: 'pointer', fontWeight: 'bold', ...props.style }}
            onClick={e => {
                e.stopPropagation()
                handleClick()
            }}
        >
            {userAvatar(id)}
            {userName(id)}
        </span>
    )
}

const mapStateToProps = state => {
    return {
        progressData: state.progressData,
    }
}

export default withRouter(connect(mapStateToProps)(ProgressButton))
