import React, { useEffect } from 'react'
import { Route, Switch, withRouter, Redirect } from 'react-router-dom'

import Auth from './containers/Auth/Auth'
import AuthTest from './containers/Auth/AuthTest'
import './components/UI/loadStyles'
import AccountLayout from './components/Layout/Layout'
import Progresses from './containers/Progresses/Progresses'
import Progress from './containers/Progress/Progress'
import Shop from './containers/Shop/Shop'
import Dashboard from './containers/Dashboard/Dashboard'
import Explore from './containers/Explore/Explore'
import Blog from './containers/Blog/Blog'

import Friends from './containers/Friends/Friends'
import Settings from './containers/Settings/Settings'
import Profile from './containers/Profile/Profile'

import ActivateAccount from './containers/ActivateAccount/ActivateAccount'
import Cookies from 'universal-cookie'
import Perk from './containers/Shop/Perk'

import WishlistItem from './containers/Wishlist/WishlistItem'
import Wishlist from './containers/Wishlist/Wishlist'
import { connect } from 'react-redux'
import * as actions from './store/actions'
import ConnectWebsocket from './containers/ConnectWebsocket/ConnectWebsocket'
import NotificationsTracker from './containers/Dashboard/NotificationsTracker'

const cookies = new Cookies()

const App = props => {
    const { isAuthed } = props
    useEffect(() => {
        props.tryAuth(props)
    }, [])

    const redirectTo = cookies.get('redirectto')

    if (redirectTo) {
        cookies.remove('redirectto', {
            path: '/',
        })
    }

    const Logout = () => {
        props.logout()
        return <Redirect to="/explore" />
    }

    let routes = (
        <Switch>
            <Route path="/" exact component={Explore} />

            <Route path="/inspirations" exact component={Progresses} />
            <Route path="/inspirations" component={Progress} />

            <Route path="/profile" component={Profile} />

            <Route path="/friends" exact component={Friends} />

            <Route path="/settings" exact component={Settings} />
            <Route path="/account" exact component={Profile} />
            <Route path="/dashboard" exact component={Dashboard} />

            <Route path="/shop" exact component={Shop} />
            <Route path="/shop" component={Perk} />
            <Route path="/wishlist" exact component={Wishlist} />
            <Route path="/wishlist" component={WishlistItem} />
            <Route path="/logout" exact component={Logout} />
            <Route path="/ru" component={Blog} />
            <Route path="/blog" component={Blog} />

            <Route path="/activate-account" exact component={ActivateAccount} />

            {process.env.NODE_ENV === 'development' && !isAuthed && (
                <Route path="/logintest" exact component={AuthTest} />
            )}
            <Redirect to="/" />
        </Switch>
    )

    if (props.accountNotActive) {
        routes = (
            <Switch>
                {!isAuthed && <Route path="/login" exact component={Auth} />}
                <Route path="/logout" exact component={Logout} />

                <Route
                    path="/activate-account"
                    exact
                    component={ActivateAccount}
                />

                {process.env.NODE_ENV === 'development' && !isAuthed && (
                    <Route path="/logintest" exact component={AuthTest} />
                )}
                {isAuthed ? (
                    <Redirect to="/activate-account" />
                ) : (
                    <Redirect to="/login" />
                )}
            </Switch>
        )
    }

    return redirectTo ? (
        <Redirect to={redirectTo} />
    ) : (
        <>
            <div style={{ height: '100%' }}>
                <AccountLayout>{routes}</AccountLayout>
                {isAuthed && <ConnectWebsocket />}
                {isAuthed && <NotificationsTracker />}
            </div>
            {/* {this.props.loading && <Spinner cover />} */}
        </>
    )
}

const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        user: state.user,
        account: state.account,
        accountNotActive: state.accountNotActive,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        tryAuth: (props, forceAuth) =>
            dispatch(actions.tryAuth(props, forceAuth)),
        logout: () => dispatch(actions.logout()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(App)
)
